$controlHeight: 30px;

.item {
  display: flex;
  align-items: center;
  padding: 0.5rem var(--PSPDFKit-spacing-4);
  height: 46px;

  border-bottom: 1px solid var(--PSPDFKit-FormDesignerPopoverComponent-item-border-color);

  &:last-child {
    border-bottom: none;
  }
}

.itemIcon {
  display: flex;
  width: 24px;
  height: 24px;
}

.itemLabel {
  margin: 0 1rem;
  flex: 1 0;
}

.itemLabel,
.advancedItemLabel {
  font-size: var(--PSPDFKit-fontSize-3);
  color: var(--PSPDFKit-FormDesignerPopoverComponent-item-color);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.colorDropdown {
  height: $controlHeight;

  & :global(.PSPDFKit-Input-Color-Dropdown) {
    margin-right: 0;
  }
}

.colorDropdownMenu {
  background-color: var(--PSPDFKit-FormDesignerPopoverComponent-color-dropdown-menu-color);
}

.sliderDropdown {
  height: $controlHeight;
  margin: 0;

  & :global(.PSPDFKit-Input-Dropdown-Items) {
    transform: translateX(-78%) !important;
  }
}

.colorSvg {
  width: 20px;
  height: 20px;
}

.colorItem {
  min-width: 30px;
  height: $controlHeight;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 5px;
}

.nativeDropdown {
  display: none;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: block;
    align-items: center;
  }
}

.dropdownGroupComponent {
  display: flex;
  align-items: center;
  height: $controlHeight;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }
}

.dropdownGroupMenu {
  padding: 0;
}

.selectBox {
  height: 32px;
}

.advancedItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem var(--PSPDFKit-spacing-4);
  height: 46px;
  border-top: 1px solid var(--PSPDFKit-FormDesignerPopoverComponent-item-border-color);
}

.advancedItemValue {
  font-size: var(--PSPDFKit-fontSize-2);
  flex: 1 0;
  text-align: right;
  color: var(--color-coolGrey400);
}

.radioButtonWarning {
  padding: 0 var(--PSPDFKit-spacing-4) var(--PSPDFKit-spacing-4);
  display: flex;
  align-items: center;
}

.radioButtonWarningIcon {
  height: 16px;
  width: 16px;
  margin-right: 8px;
}

.radioButtonWarningLabel {
  color: var(--PSPDFKit-FormDesignerPopoverComponent-warning-label-color);
  font-size: var(--PSPDFKit-fontSize-2);
  line-height: var(--PSPDFKit-lineHeight-2);
}

.formFieldNameReadOnly {
  position: relative;

  & > div:first-child {
    cursor: not-allowed;
  }
}

.readOnlyIcon {
  position: absolute;
  right: 15px;
  top: 43px;
  cursor: not-allowed;
  color: var(--PSPDFKit-FormDesignerPopoverComponent-readonly-icon-color);
}

.inputReadOnly {
  pointer-events: none;

  input {
    background-color: transparent !important;
  }
}

.lastInputField {
  padding-bottom: var(--PSPDFKit-spacing-3) !important;
}

.footerButton {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-md) &,
  :global(.PSPDFKit-width-sm) & {
    width: 100px;
    border-radius: 3px;
    padding: var(--PSPDFKit-spacing-7) 0;
  }
}

.deleteButton {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-md) &,
  :global(.PSPDFKit-width-sm) & {
    border: 1px solid var(--PSPDFKit-FormDesignerPopoverComponent-deleteButton-border);
    margin-right: 8px;
    color: var(--PSPDFKit-FormDesignerPopoverComponent-deleteButton-mobile-text-color);

    /* stylelint-disable-next-line */
    &:hover {
      background-color: initial;
    }
  }
}

.doneButton {
  display: none;
  border: 1px solid var(--PSPDFKit-FormDesignerPopoverComponent-doneButton-border);
  background-color: var(--PSPDFKit-FormDesignerPopoverComponent-doneButton-background);
  color: var(--PSPDFKit-FormDesignerPopoverComponent-doneButton-color);

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-md) &,
  :global(.PSPDFKit-width-sm) & {
    display: block;

    /* stylelint-disable-next-line */
    &:hover {
      background-color: var(--PSPDFKit-FormDesignerPopoverComponent-doneButton-background);
    }
  }
}

:global([class*='PSPDFKit-Form-Creator-Editor-Horizontal-Alignment-']:not(.PSPDFKit-Form-Creator-Editor-Horizontal-Alignment-active)),
:global([class*='PSPDFKit-Form-Creator-Editor-Vertical-Alignment-']:not(.PSPDFKit-Form-Creator-Editor-Vertical-Alignment-active)) {
  background-color: var(--PSPDFKit-FormDesignerPopoverComponent-radioGroup-background-color);
}
