@use '@/styles/config/variables' as *;

// stylelint-disable-next-line
@use './AnnotationToolbar';

.container {
  display: flex;
  flex-direction: row;
  height: 44px;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
  box-shadow: 0 1px 2px rgba($black, 0.1);
  border-bottom: 1px solid var(--PSPDFKit-CropToolbarComponent-border);
}

.buttonContainer {
  display: flex;
}

.cropButton {
  justify-content: center;
  display: flex;
  cursor: pointer;
  padding-right: 10px;
  padding-left: 10px;
  height: 44px;
  align-items: center;
  transition: background-color 0.15s;

  color: var(--PSPDFKit-CropToolbarComponent-text-color);

  &:not(:last-child) {
    border-right: 1px solid var(--PSPDFKit-CropToolbarComponent-button-isDeselected-border);
  }

  &:hover {
    /* stylelint-disable-next-line */
    &:not([aria-disabled='true']) {
      background-color: var(
        --PSPDFKit-CropToolbarComponent-button-isHovered-backgroundColor
      ) !important;
    }
  }

  &:first-of-type {
    border-left: none !important;
    padding-left: 0;
  }

  span,
  svg {
    height: $annotation-toolbar-button-height;
    display: flex;
    align-items: center;
  }
}

.cropButtonDisabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.cancelButton {
  @extend .button;
  padding: 0 20px;
  border-radius: 3px;
  border: 1px solid !important;
  border-color: var(--PSPDFKit-CropToolbarComponent-button-borderColor) !important;
  cursor: pointer;
  transition: border-color 0.15s;

  &:hover {
    border: 1px solid !important;
    border-color: var(--PSPDFKit-CropToolbarComponent-button-isHovered-borderColor) !important;
  }

  span {
    height: $annotation-toolbar-button-height;
    display: flex;
    align-items: center;
  }
}

.cancelDesktop {
  display: block;
}

.cancelMobile {
  display: none;
  align-items: center;
  padding-right: 0;
  padding-left: 0;
  justify-content: center;
  border-left: 1px solid var(--PSPDFKit-CropToolbarComponent-button-isDeselected-border);
}

.desktopButtonText {
  display: block;
}

.mobileButtonText {
  display: none !important;
}

@media (max-width: 768px) {
  .cancelDesktop {
    display: none;
  }

  .cancelMobile {
    display: flex;
    width: 47px;

    svg {
      height: 17px;
      width: 17px;
      stroke-width: 2px;
    }
  }

  .container {
    padding: 0;
  }

  .desktopButtonText {
    display: none !important;
  }

  .mobileButtonText {
    display: flex !important;
  }
}
