/* @PSPDFKit-namespace DocumentComparison */
@use 'sass:math';

// stylelint-disable-next-line
@use '@/styles/config/variables' as *;

/* dimensions taken from figma: https://www.figma.com/file/aQR2NaStRUsmLJhVTdbRgq/Comparison-UI?node-id=578%3A14655 */
$crosshair-width: 82px;
$crosshair-height: 82px;

.docComparison {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.source,
.sourceTouch {
  align-self: stretch;
  flex-grow: 1;
}

.referencePoint {
  position: absolute;
  width: 52px;
  height: 55px;
  transform: translate(-27px, -28.5px);
}

.crosshair {
  position: fixed;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(
    math.div(-$crosshair-width, 2),
    -(math.div($crosshair-height, 2) - math.div($toolbar-height, 2))
  );
  width: $crosshair-width;
  height: $crosshair-height;

  svg {
    height: $crosshair-height;
    width: $crosshair-width;
    pointer-events: none;
  }

  path {
    /* stylelint-disable-next-line */
    filter: drop-shadow(0px 3px 4px rgba(43, 50, 59, 0.2));

    /* stylelint-disable-next-line */
    &:nth-child(2) {
      stroke: var(--PSPDFKit-DocumentComparison-crosshair-stroke);
    }
  }
}

.selectPoint {
  position: absolute;
  top: -55px;

  /*  https://www.figma.com/file/aQR2NaStRUsmLJhVTdbRgq/Comparison-UI?node-id=578%3A14655 */
  height: 45px;
  width: 117px;
  color: #fff;
  background-color: #000;
  border-radius: 8px;
  transform: translateX(-50%);
  left: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  pointer-events: all;
  transition: 0.25s;

  &.hidden {
    pointer-events: none;
    opacity: 0;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    height: 10px;
    width: 10px;
    background-color: #000;
  }
}

.comparisonPin path {
  filter: drop-shadow(0 3px 4px rgba(43, 50, 59, 0.2));
}

.page {
  position: relative;
  display: block;
  background: white;
  overflow: hidden;

  &.deg-0 {
    box-shadow: 3px 3px 8px rgba($black, 0.1);
  }

  &.deg-90 {
    box-shadow: 3px -3px 8px rgba($black, 0.1);
  }

  &.deg-180 {
    box-shadow: -3px -3px 8px rgba($black, 0.1);
  }

  &.deg-270 {
    box-shadow: -3px 3px 8px rgba($black, 0.1);
  }
}

$magnifier-size: 174px;

.magnifierContainer {
  position: fixed;
  width: $magnifier-size;
  height: $magnifier-size;
  border-radius: 50%;
  border-width: 1px;
  border-color: var(--PSPDFKit-DocumentComparison-magnifierContainer-borderColor);
  background-color: var(--PSPDFKit-DocumentComparison-background);
  border-style: solid;
  box-shadow: 0 0 0 1px var(--PSPDFKit-DocumentComparison-magnifierContainer-boxShadow);
  overflow: hidden;

  /*
  Hardcoded values from the Figma design:
  https://www.figma.com/file/aQR2NaStRUsmLJhVTdbRgq/Comparison-UI?node-id=578%3A13331 */
  right: 30px;
  top: 30px + $toolbar-height * 2;
}

.magnifier {
  position: absolute;
}

.magnifierGridContainer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.magnifierGridStroke {
  stroke: var(--PSPDFKit-DocumentComparison-magnifierGrid-stroke);
  fill: none;
}

.magnifierCenterOutlineStroke {
  stroke: var(--PSPDFKit-DocumentComparison-magnifierCenterOutlineStroke-stroke);
  fill: none;
}

.magnifierCenterStroke {
  stroke: var(--PSPDFKit-DocumentComparison-magnifierCenterStroke-stroke);
  fill: none;
}

.magnifierZoomValueBackground,
.magnifierZoomValueText {
  position: absolute;
  left: 50%;
  top: 75%;
  transform: translate(-50%, -50%);

  /* Hardcoded values from the Figma design:
  https://www.figma.com/file/aQR2NaStRUsmLJhVTdbRgq/Comparison-UI?node-id=578%3A13331 */
  border-radius: 10px;
  font-size: 12px;
  min-width: 100px;
  height: 20px;
  line-height: 20px;
}

.magnifierZoomValueBackground {
  background-color: var(--PSPDFKit-DocumentComparison-magnifierZoomValue-backgroundColor);
  mix-blend-mode: multiply;
}

.magnifierZoomValueText {
  color: var(--PSPDFKit-DocumentComparison-magnifierZoomValue-color);
  text-align: center;
}

.documentComparisonPageLoading {
  position: absolute;
  left: 50%;
  top: 50%;
}

.pointerLayer {
  pointer-events: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
