@use '@/styles/config/variables' as *;

.layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.annotation {
  position: absolute;
  pointer-events: auto;
  border: 0;
  padding: 0;
  background: transparent;
  line-height: 1;

  &:hover,
  &:focus {
    cursor: pointer;
  }

  &[disabled] {
    cursor: auto;
  }
}

.icon {
  fill: currentColor;
  max-width: 100%;
  display: block;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 10px;
  line-height: 1.2em;
  overflow-y: auto;
  font-family: sans-serif;
  cursor: text;
  width: 300px;
  height: 200px;
  white-space: pre-wrap;
  word-break: break-word;
  word-wrap: break-word;

  .content {
    margin-top: 10px;
  }
}

.fullScreen {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: $white;
  text-align: initial;
  margin: 0;
  padding: 15px 7px 15px 15px;

  .content {
    margin-top: 15px;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  cursor: pointer;
  padding: 0;
  margin: 0;
  opacity: 0.7;
  width: 24px;
  height: 24px;
  border: transparent;
  background: transparent;

  &:hover,
  &:focus,
  &:active {
    opacity: 1;
  }

  span {
    width: 20px;
    height: 20px;
  }

  &.hidden {
    visibility: hidden;
    pointer-events: none;
  }
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  .title {
    margin: 0;
    opacity: 0.8;
    text-shadow: 0 1px 1px $white;
    font-size: var(--PSPDFKit-fontSize-3);
  }
}

.content {
  background: transparent;
  position: relative;
  flex: 1;
  overflow-y: auto;
  text-align: left;
}

.tools {
  display: flex;
  justify-content: center;
  max-width: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
}

.tool {
  opacity: 0.7;

  &:hover,
  &:focus,
  &[data-focus-within],
  &:active {
    opacity: 1;
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    opacity: 1;
  }
}
