@use '@/styles/config/variables' as *;

$thumb-height: 16px;
$track-height: 8px;

.root {
  display: flex;
  align-items: center;
  font-size: inherit;
  color: inherit;
  margin: 5px;

  // prevent Safari clipping the thumb
  line-height: $thumb-height * 2;

  position: relative;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.slider {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
}

.label {
  padding-right: 6px;
}

.value {
  text-align: right;
  white-space: nowrap;
  line-height: 1;
  cursor: pointer;
}

.dropdownItems {
  position: absolute;
  top: 100%;
  background: var(--PSPDFKit-SliderDropdownComponent-items-background);
  z-index: $zindex-dropdown;
  display: none;
  min-width: 100%;
  overflow: visible;
  margin: 0;
  text-align: left;
  list-style: none;
  border: 1px var(--PSPDFKit-SliderDropdownComponent-items-border) solid;

  &:focus {
    outline: none;
  }

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    left: auto !important;
    right: 0;
  }
}

.dropDownItemsUp {
  top: auto;
  bottom: 100%;
}

.button {
  background: var(--PSPDFKit-SliderDropdownComponent-button-background);
  width: 100%;
  border: 1px var(--PSPDFKit-SliderDropdownComponent-button-border) solid;
  min-width: 5em;
  text-align: center;
  cursor: pointer;
  padding-right: 20px;
  border-radius: $border-radius-sm;
  color: var(--PSPDFKit-SliderDropdownComponent-button-color);
  font: inherit;
  line-height: 1;

  &:hover {
    /* stylelint-disable-next-line selector-max-specificity */
    .toggle {
      fill: var(--PSPDFKit-SliderDropdownComponent-button-isHovered-color);
    }
  }
}

.flexContainer {
  display: flex;
}

.toggle {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  line-height: 1;

  fill: var(--PSPDFKit-SliderDropdownComponent-button-toggle-color);

  .show & {
    fill: var(--PSPDFKit-SliderDropdownComponent-button-show-color);
  }

  svg {
    width: 12px;
    height: 12px;
  }
}

.dropdownItem {
  display: block;
  padding: 10px;
  clear: both;
  white-space: nowrap; // prevent links from randomly breaking onto new lines
  color: var(--PSPDFKit-SliderDropdownComponent-item-color);
  text-decoration: none;
  border-bottom: 1px var(--PSPDFKit-SliderDropdownComponent-item-border) solid;
  overflow: hidden;

  &:last-child {
    border-bottom: 0;
  }
}

.isFocused {
  background: --PSPDFKit-SliderDropdownComponent-item-isFocused-background;
  color: var(--PSPDFKit-SliderDropdownComponent--item-isFocused-color);
}

.selected {
  background: --PSPDFKit-SliderDropdownComponent-item-isSelected-background;
}

.disabled {
  color: var(--PSPDFKit-SliderDropdownComponent-item-isDisabled-color);
  opacity: var(--PSPDFKit-SliderDropdownComponent-item-isDisabled-opacity);
}

.isFocused.selected {
  font-weight: bold;
  background: var(--PSPDFKit-SliderDropdownComponent-item-isFocused-isSelected-background);
}

.show {
  > .dropdownItems {
    display: block;
    left: 50%;

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-width-lg) &,
    :global(.PSPDFKit-width-xl) & {
      transform: translateX(-50%);
    }
  }

  // Remove the outline when :focus is triggered
  > a {
    outline: 0;
  }
}

.dropdown {
  position: absolute;
  width: auto;
  height: auto;
  background: var(--PSPDFKit-SliderDropdownComponent-dropdown-background);
  margin-top: 5px;
  z-index: $zindex-annotation-toolbar;
  color: var(--PSPDFKit-SliderDropdownComponent-dropdown-color);
  opacity: 1;

  border: 1px solid var(--PSPDFKit-SliderDropdownComponent-dropdown-border);
  border-radius: $border-radius-sm;
  /* stylelint-disable */
  box-shadow: 0 3px 12px var(--PSPDFKit-shadow-color);
  /* stylelint-enable */
}

.srLabel {
  position: static;
}
