/* ConfirmModalComponent */

.confirmGroup {
  display: flex;
  justify-content: flex-end;
  margin-top: 1em;
}

.content :global(p) {
  margin: 0;
}

.button {
  &:focus {
    border-color: var(--PSPDFKit-ConfirmModalComponent-button-isFocused-border) !important;
  }

  &:active,
  &[data-focus-within] {
    border-color: var(--PSPDFKit-ConfirmModalComponent-button-isFocused-border) !important;
    outline: none;
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    border-color: var(--PSPDFKit-ConfirmModalComponent-button-isFocused-border) !important;
    outline: none;
  }
}
