@use '@/styles/config/variables' as *;

.container,
.canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: default;
  user-select: none;
}

.container {
  pointer-events: none;
}

// Disable touch actions when interactions get disabled, otherwise
// the user would scroll when creating an ink annotation or moving it
:global(.interactions-disabled) .canvas {
  touch-action: none;
}

.path {
  fill: none;
  user-select: none;
  stroke-linejoin: round;
  stroke-linecap: round;

  &.selectable {
    pointer-events: stroke;
    cursor: pointer;

    // This prevents the blue flash when tapping elements on mobile
    // TODO(@meleyal) remove when we have annotation support on mobile
    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-supportsTouch.PSPDFKit-width-xs) &,
    :global(.PSPDFKit-supportsTouch.PSPDFKit-width-sm) & {
      pointer-events: none;
    }
  }
}

.rect.selectable {
  pointer-events: fill;
  cursor: pointer;
}

.svg {
  user-select: none;
  pointer-events: none;
  position: absolute;
}
