//== Colors
$black: #000;
$white: #fff;
$gray: #5f5f5f;
$gray-dark: #3d464d;
$gray-light: #eee;
$blue: #3c97fe;
$blue-light: #f3f7fd;
$blue-light-darken: darken($blue-light, 10%);
$yellow: #f5f23b;
$error-red: #db2a37;
$custom-text-selection-color: rgb(35 120 255);
$selection-rect-color: rgba($blue, 0.5);
$selection-rect-stroke: $white;
$resize-anchor-color: $blue;
$input-bg: $white;
$toolbar-bg: $gray-dark;

$toolbar-color: #83888d;
$toolbar-hover: lighten($toolbar-bg, 15%);
$annotation-toolbar-bg: $gray-light;
$annotation-toolbar-color: #787878;
$annotation-toolbar-hover: darken($annotation-toolbar-bg, 5%);

$tooltip-bg: $toolbar-bg;
$search-bg: #2d3133;

//== Transitions
$transition-duration: 150ms;

//== Layers
$zindex-sidebar: 1;
$zindex-toolbar: 3;
$zindex-annotation-toolbar: 2;
$zindex-responsive-group: 4;
$zindex-dropdown: 100;
$zindex-search: 10;
$zindex-popover: 1000;
$zindex-modal-background: 1001;
$zindex-modal: 1002;
$zindex-background: -1;

//== Dimensions
$toolbar-height: 44px;
$annotation-toolbar-button-height: 32px;
$border-radius-sm: 3px;
$border-radius-md: 6px;
$dialog-border-radius: 6px;
$active-textmarkup-padding: 3px;
$active-redaction-padding: 6px;

//== Media query breakpoints
$screen-xs: 480px;
$screen-sm: 768px;
$screen-md: 992px;
$screen-lg: 1200px;

$widget-annotation-color: #ecf0ff;
$widget-read-only-color: #f0f0f0;
$widget-annotation-focus-background-color: #fffb8b;
$widget-annotation-focus-color: #333;

// Easing functions from easings.net
// Arranged from top to bottom in order of less to more steep. Steeper curves
// will generally require a longer duration so that they do not appear jerky.

$ease-out-cubic: cubic-bezier(0.215, 0.61, 0.355, 1);
$ease-out-quart: cubic-bezier(0.165, 0.84, 0.44, 1);
$ease-out-expo: cubic-bezier(0.16, 1, 0.3, 1);

$ease-in-cubic: cubic-bezier(0.55, 0.055, 0.675, 0.19);
$ease-in-quart: cubic-bezier(0.895, 0.03, 0.685, 0.22);

$es-wrapper-height: 465px;
$es-wrapper-height-screen-xs: 368px;
$es-wrapper-width-screen-xs: 100%;

$es-signature-area-width: 640px;
$es-signature-area-heigh: 263px;
$es-signature-area-width-screens-md: 500px;
$es-signature-area-width-screens-xs: 95%;
$es-signature-area-height-screens-xs: 163px;

$es-border-radius: 5px;
$es-input-border-radius: 50%;

$es-typingTab-typingUIContainer-height: 45%;
$es-typingTab-typingUIContainer-height-screen-xs: 53%;
$es-typingTab-fontFamiliesPickerContainer-height: 55%;
$es-typingTab-fontFamiliesPickerContainer-height-screen-xs: 47%;

$es-typingTab-input-height: 86px;

$es-actionContainer-height: 30px;
