/** @PSPDFKit-define Pager **/

@use '@/styles/config/variables' as *;

$input-height: 14px;

.root {
  display: flex;
  cursor: default;
  user-select: none;
  padding: 0 15px;
  color: var(--PSPDFKit-Pager-color);
  font-variant-numeric: tabular-nums;
  width: fit-content;
  font-size: var(--PSPDFKit-fontSize-3);
}

.desktop,
.mobile {
  display: flex;
  align-items: center;
}

.desktop {
  min-width: 0;
  /* stylelint-disable-next-line selector-max-specificity */
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) & {
    display: none;
  }
}

.mobile {
  display: none;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) & {
    display: flex;
  }
}

.label {
  strong {
    font-weight: normal;
    color: var(--PSPDFKit-Pager-color);
  }
}

.control {
  display: flex;
  height: $toolbar-height - 18px;
  margin: 0 var(--PSPDFKit-spacing-6);
}

.input {
  width: 48px;
  text-align: center;
  padding: 4px;
  font-weight: normal;
  background: var(--PSPDFKit-Pager-input-background);
  border: none;
  color: var(--PSPDFKit-Pager-input-color);
  transition: all $transition-duration;
  margin: 0 1px;
  font-size: var(--PSPDFKit-fontSize-3);

  &:focus,
  &:active {
    color: var(--PSPDFKit-Pager-input-isActive-color);
  }

  // Turn off number spinner on Mozilla and Webkit
  -moz-appearance: textfield; /* stylelint-disable-line */

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none; /* stylelint-disable-line */
  }
}

.prev,
.next {
  width: 26px;
  height: 100%;
  background: var(--PSPDFKit-Pager-input-background);
  color: var(--PSPDFKit-Pager-input-isActive-color);
  border: 0;
  cursor: pointer;
  transition: all $transition-duration;
  display: flex;
  justify-content: center;
  padding: 5px 3px;
  touch-action: none;

  .icon {
    width: 14px;
    height: 14px;
    display: block;
    fill: var(--PSPDFKit-Pager-input-isActive-color);
  }

  &:hover,
  &:focus {
    background: var(--PSPDFKit-Pager-input-isHovered-background);

    /* stylelint-disable-next-line selector-max-specificity */
    .icon {
      fill: var(--PSPDFKit-Pager-input-isHovered-color);
    }
  }

  &:disabled {
    pointer-events: none;

    /* stylelint-disable-next-line selector-max-specificity */
    .icon {
      fill: var(--PSPDFKit-Pager-input-isDisabled-color);
      opacity: var(--PSPDFKit-Pager-input-isDisabled-opacity);
    }
  }
}

.prev {
  border-radius: 4px 0 0 4px;
}

.next {
  border-radius: 0 4px 4px 0;
}

.inputLabel {
  position: absolute;
  top: -1000000px;
  left: -1000000px;
}
