.wrapper {
  padding: var(--PSPDFKit-spacing-4) var(--PSPDFKit-spacing-4) var(--PSPDFKit-spacing-3);
}

.title {
  text-transform: uppercase;
  color: var(--PSPDFKit-FormDesignerOptionsControlComponent-title-color);
  padding-bottom: var(--PSPDFKit-spacing-4);
  font-size: var(--PSPDFKit-fontSize-2);
}

.option {
  display: flex;
  align-items: center;
  padding: var(--PSPDFKit-spacing-5) 0;
  border-top: 1px solid var(--PSPDFKit-FormDesignerOptionsControlComponent-border-color);
  min-height: 46px;
}

.icon {
  width: 16px;
  color: var(--PSPDFKit-FormDesignerOptionsControlComponent-icon-color);
}

.button {
  appearance: none;
  border: none;
  background: none;
  height: 23px;

  &:hover {
    cursor: pointer;
  }
}

.optionContent {
  margin: 0 1rem 0 0;
  flex: 1 0;
  font-size: var(--PSPDFKit-fontSize-3);
  color: var(--PSPDFKit-FormDesignerOptionsControlComponent-text-color);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.deleteButton {
  display: flex;
  &:hover .deleteIcon {
    color: var(--PSPDFKit-FormDesignerOptionsControlComponent-hover-color);
  }
}

.addOptionButton {
  color: var(--PSPDFKit-FormDesignerOptionsControlComponent-addOption-button-color);
  text-decoration: underline;
  padding-top: 6px;
  padding-left: 0;
  font-size: var(--PSPDFKit-fontSize-3);
}

.addOptionInputWrapper {
  display: flex;
  flex-direction: column;
  height: unset;
  align-items: flex-start;
  border-bottom: 1px solid var(--PSPDFKit-FormDesignerOptionsControlComponent-border-color);
}

.addOptionInput {
  border: none;
  background: none;
  display: inline-block;
  height: 30px;
  width: 100%;
  margin-right: 0;
  flex: 1 1 auto;
  &:focus {
    outline: none;
  }

  &::placeholder {
    color: var(--color-coolGrey400);
  }
}

.hamburger {
  background-color: transparent;
  border: none;
  display: inline-flex;
  padding: 0;
  margin-right: var(--PSPDFKit-spacing-5);
  cursor: move;
  color: var(--PSPDFKit-FormDesignerOptionsControlComponent-icon-color);

  span {
    display: inline-flex;
  }

  &:hover {
    color: var(--PSPDFKit-FormDesignerOptionsControlComponent-addOption-button-color);
  }
}

.preview {
  background-color: var(--PSPDFKit-FormDesignerOptionsControlComponent-background-color);
  opacity: 0.5;
  width: 332px;
  border-top: none;
}

.invalidOptionInput {
  font-size: var(--PSPDFKit-fontSize-2);
  color: var(--PSPDFKit-FormDesignerTextInputComponent-error-color);
  width: 100%;
}

.addOptionInputWrapperInvalid {
  border-bottom-color: var(--PSPDFKit-FormDesignerTextInputComponent-error-color);
}
