:root {
  --PSPDFKit-fontFamily-1: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --PSPDFKit-lineHeight-1: 1.4rem;
  --PSPDFKit-lineHeight-2: 0.815rem;
  --PSPDFKit-fontSize-1: 1rem;
  --PSPDFKit-fontSize-2: 0.75rem;
  --PSPDFKit-fontSize-3: 0.875rem;
  --PSPDFKit-fontSize-4: 1.125rem;
  --PSPDFKit-fontSize-5: 1.25rem;
  --PSPDFKit-fontSize-6: 1.5rem;
}
