@use '@pspdfkit/ui/theme' as *;

.root {
  color: $color-white;
  padding: 0 $borderRadius-3;
}

.item {
  border-right: 1px solid darken($color-darkBlue, 23%);

  &:last-child {
    border-right: 0;
  }

  &:hover,
  &:focus,
  &[data-focus-within],
  &:active {
    background-color: lighten($color-gray2, 15%);
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background-color: lighten($color-gray2, 15%);
  }
}
