@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

$signature-mq: 350px;

/* sign label for form fields */

.sign {
  display: inline-flex;
  align-items: center;
  font-size: var(--PSPDFKit-fontSize-2);
  padding: 0.2em 0.4em;
  background-color: var(--PSPDFKit-Signatures-background);
  color: var(--PSPDFKit-Signatures-color);
  border: 1px solid var(--PSPDFKit-Signatures-border);
  position: absolute;
  top: 0;
  left: 0;
  text-transform: lowercase;
  line-height: 1;
  border-radius: 0 0 0.2em;
}

.signReadOnly {
  color: inherit;
  background-color: inherit;
  border-color: inherit;
}

/* signature specific modal styles */

.modal {
  width: 100%;
  max-width: 640px;
  max-height: 100vh;

  @media (max-height: 455px) {
    height: 90%;
  }

  @media (max-height: 300px) {
    height: 100%;
  }

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-width: 100%;
    height: 100%;
    border-radius: 0;
  }

  .editorCanvasContainer,
  .signaturePickerContainer {
    margin-left: calc(-1 * var(--PSPDFKit-spacing-5));
    margin-right: calc(-1 * var(--PSPDFKit-spacing-5));
  }
}

/* view is basically the container for the entire feature
and it is rendered inside of a modal */

.viewRoot {
  color: var(--PSPDFKit-Signatures-container-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.viewSignaturePicker {
  height: auto;
  max-height: 60vh;

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-height: 100%;
    height: 100%;
  }
}

/* layout */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: var(--PSPDFKit-spacing-3);
}

.footer {
  margin-top: var(--PSPDFKit-spacing-3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.title,
.colorPicker {
  margin-bottom: var(--PSPDFKit-spacing-6);

  @media (min-width: $signature-mq) {
    margin-bottom: 0;
  }
}

@media (max-width: $signature-mq) {
  .button {
    width: 100%;

    & ~ & {
      margin-top: var(--PSPDFKit-spacing-5);
    }
  }

  .group {
    width: 100%;
    margin-top: var(--PSPDFKit-spacing-5);
  }
}

/* signature editor */

.editorCanvas {
  position: relative;
  width: 100%;
  padding-bottom: 43.3333%;
  overflow: hidden;
  border-radius: $borderRadius-5;
  border: 1px solid var(--PSPDFKit-Signatures-editorCanvas-border);
  background-color: var(--PSPDFKit-Signatures-editorCanvas-background);
}

.signHere {
  position: absolute;
  top: 50%;
  margin-top: -0.5em;
  width: 100%;
  text-align: center;
  color: var(--PSPDFKit-Signatures-signHere-color);
  user-select: none;
  transition: opacity 200ms linear, visibility 200ms;
}

.signHereHidden {
  opacity: 0;
  visibility: hidden;
}

.checkbox {
  display: flex;
  align-items: center;
}

.checkboxLabel {
  margin-left: 0.5em;
}

.colorPicker {
  font-size: var(--PSPDFKit-fontSize-6) !important;
}

/* signature picker */

.signaturePickerContainer {
  max-height: 50vh;
  overflow: auto;
  flex: 1 1 auto;

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-height: 100%;
  }
}

.signaturePickerList {
  width: 100%;
  margin: 0;
  padding: 0;
  display: block;
  padding-bottom: 1rem;
}

.signaturePickerListItem {
  margin-top: var(--PSPDFKit-spacing-7);
  list-style: none;
  position: relative;

  &:first-child {
    margin-top: 0;
  }
}

// In the signature picker we render the available signatures as clickable buttons

.selectSignatureButton {
  display: block;
  width: 100%;
  padding-bottom: 20%;
  opacity: 0.75;
  touch-action: auto;

  &:hover,
  &:focus,
  &:active {
    opacity: 1;
  }

  &.imageAnnotation {
    padding: calc(var(--PSPDFKit-spacing-5) / 2) var(--PSPDFKit-spacing-5);
  }
}

.deleteSignatureButton {
  position: absolute;
  top: var(--PSPDFKit-spacing-6);
  right: var(--PSPDFKit-spacing-6);
  width: 24px;
  height: 24px;
  color: var(--PSPDFKit-Signatures-DeleteSignatureButton-color);
  border: 0;
  padding: 0;
  background: transparent;
  opacity: 0.3;
  cursor: pointer;

  &:hover,
  &:focus,
  &:active {
    opacity: 1;
  }

  svg {
    width: 100%;
    height: 100%;
  }
}

.title {
  color: var(--PSPDFKit-Signatures-Title-color);
  font-size: var(--PSPDFKit-fontSize-5);
}
