@use '@/styles/config/variables' as *;

.container,
.canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  user-select: none;
  cursor: crosshair;
}

.container {
  pointer-events: none;
}

// Disable touch actions when interactions get disabled, otherwise
// the user would scroll when creating a shape annotation or moving it
:global(.interactions-disabled) .canvas {
  touch-action: none;
}

.visible {
  pointer-events: none;
  user-select: none;
  shape-rendering: geometricPrecision;
  stroke-linecap: butt;
  stroke-linejoin: miter;

  &.cloudy {
    stroke-linecap: round;
    stroke-linejoin: round;

    /* stylelint-disable-next-line selector-max-specificity */
    &.strokeDashArray {
      stroke-linecap: butt;
      stroke-linejoin: miter;
    }
  }
}

.selectable {
  pointer-events: painted;
  cursor: pointer;
  stroke-linecap: butt;
  stroke-linejoin: miter;

  &.cloudy {
    stroke-linecap: round;
    stroke-linejoin: round;
  }
}

.svg {
  user-select: none;
  pointer-events: none;
  position: absolute;
}
