@use '@/styles/config/variables' as *;

.checkradio {
  display: block;
  position: relative;

  [type='checkbox'],
  [type='radio'] {
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;

    /* stylelint-disable-next-line selector-max-specificity */
    &:focus + .input {
      background-color: $widget-annotation-focus-background-color;
      border-color: darken($widget-annotation-focus-background-color, 40%);
      box-shadow: none !important;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    &:hover:not([disabled]) + .input,
    &:active:not([disabled]) + .input {
      background-color: $white;
      box-shadow: inset 0 0 0 1px darken($widget-annotation-color, 10%);
    }

    /* stylelint-disable-next-line selector-max-specificity */
    &:checked + .inputRadio:after,
    &.checked + .inputRadio:after {
      background-color: desaturate(darken($widget-annotation-color, 60%), 20%);
    }

    /* stylelint-disable-next-line selector-max-specificity */
    &:checked[disabled] + .inputRadio:after,
    &.checked[disabled] + .inputRadio:after {
      background-color: rgba(desaturate(darken($widget-read-only-color, 60%), 20%), 0.7);
    }

    /* stylelint-disable-next-line selector-max-specificity */
    &[disabled] ~ .check {
      color: lighten($black, 40%);
    }
  }
}

.input,
.check {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  line-height: 0;
  pointer-events: none;
}

.input {
  border-radius: 0.1em;
}

.inputRadio {
  border-radius: 100%;
  pointer-events: none;

  &:after {
    pointer-events: none;
    border-radius: 100%;
    content: '';
    position: absolute;
    width: 50%;
    height: 50%;
    top: 25%;
    left: 25%;
    background-color: transparent;
  }
}

.check {
  color: desaturate(darken($widget-annotation-color, 60%), 20%);

  svg {
    width: 100%;
    height: 100%;
  }
}
