@use '@/styles/config/variables' as *;

.modal {
  width: 90%;
  max-width: 1126px;
  max-height: 100vh;
  background-color: var(--PSPDFKit-ElectronicSignatures-modal-background);
  padding: 0;

  @media (max-height: 455px) {
    height: 90%;
  }

  @media (max-height: 300px) {
    height: 100%;
  }

  @media (orientation: landscape) and (max-width: $screen-xs) {
    max-width: 100%;
    border-radius: 0;
  }

  @media (max-width: 740px) {
    width: 95%;
  }

  @media (max-width: 695px) {
    width: 100%;
  }
}
