.canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  user-select: none;
  touch-action: none;
  cursor: zoom-in;
}

.svg {
  position: absolute;
  shape-rendering: crispEdges;
}

.rect {
  fill: var(--PSPDFKit-MarqueeZoom-rect-fill);
  stroke-width: 1px;
  stroke-dasharray: 5;
  stroke: black;
  opacity: 0.5;
}

// The second rect is solely used to fill the stroke between the dasharray
// offset.
.rectStrokeBetween {
  fill: transparent;
  stroke-width: 1px;
  stroke-dasharray: 5;
  stroke-dashoffset: 5;
  stroke: white;
  opacity: 0.5;
}
