.root {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  /**
   * There are two ways to prevent double-tap zooming and enable fastClick on mobile.
   * The first is using viewport the same max/min scale in viewport meta tag and disallow
   * user-scaling. We don't have control on our customers viewport meta tag, we need to use
   * second option, `touch-action`. We can not use `manipulation` because it would only disable
   * double-tap but not pinch-zooming. Since we handle pinching on our own anyway, we need
   * to just allow `pan-x` and `pan-y`.
   */
  touch-action: pan-x pan-y;

  &,
  * {
    box-sizing: border-box;
  }

  /**
   * Sometimes Safari would not turn PSPDFKit-Web into it's own layer, but put it in the layer of a
   * parent element (or have it share the layer with sibling elements), in this non-hw-accelerated
   * mode it would somehow render `<svg>`s into a bitmap, before applying the proper scaling, which
   * would lead to blurred ink annotations and selection outlines when zooming. We could reproduce this
   * undesired behaviour by adding an element with `position: fixed` in the DOM before PSPDFKit-Web's root
   * node (in our minimal case this bug went away if as the element's position didn't intersect with the
   * SVG element's bounding box, but the original bug on web-preview.pspdfkit wasn't fixed that way)
   *
   * Adding this 3D translation will force Safari to render this subtree properly as a layer, which
   * makes the `<svg>` scale as vectors instead of bitmaps.
   *
   * https://github.com/PSPDFKit/PSPDFKit-Web/issues/278
   */
  &:global(.PSPDFKit-browser-engine-webkit) {
    transform: translate3d(0, 0, 0);
  }
}

@media print {
  * {
    display: none !important;
  }
}

body {
  font-size: var(--PSPDFKit-fontSize-1);
  font-family: var(--PSPDFKit-fontFamily-1);
  line-height: var(--PSPDFKit-lineHeight-1);
  background: var(--PSPDFKit-App-background);
}

.main {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  overflow: hidden;
}

.mainContent {
  flex: 1;
}

/**
 * Rules 1. and 2. should not be next to each other
 * otherwise they are merged together.
 * When merged the grouped rules are ignored altogether when :focus-visible is not supported.
 */
// 1.
:focus:not(:focus-visible) {
  outline: none;
}

.mainContentResizing {
  user-select: none;
  opacity: 0.3;
}

// 2.
/* stylelint-disable-next-line selector-max-specificity */
:root:not([data-focusvisible-supported]) :focus:not([data-focusvisible-polyfill]) {
  outline: none;
}

.viewportContent {
  display: flex;
}
