@use '@pspdfkit/ui/src/design-tokens/color';

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.guide {
  position: absolute;
  border: 1px dashed var(--color-blue600);
}

.hGuide {
  @extend .guide;
  width: 100%;
  height: 2px;
}

.vGuide {
  @extend .guide;
  height: 100%;
  width: 2px;
}

.widgets {
  position: absolute;
  background: rgba(69, 55, 222, 0.2);
  border: 2px solid var(--color-blue70);
  height: 40px;

  & > div,
  & > span {
    display: none;
  }
}

.buttonWidget {
  @extend .widgets;
  width: 120px;
}

.textWidget {
  @extend .widgets;
  width: 220px;
}

.listBoxWidget {
  @extend .widgets;
  height: 150px;
  width: 220px;
}

.radioWidget {
  @extend .widgets;
  height: 32px;
  width: 32px;
  border-radius: 50%;
}

.checkboxWidget {
  @extend .widgets;
  height: 32px;
  width: 32px;
}

.signWidget {
  @extend .widgets;
  @extend .textWidget;
  height: 150px;
  display: flex;

  & > div {
    display: inline-block;
    background-color: var(--color-blue600);
    color: var(--color-white);
    align-self: flex-start;
    font-size: 14px;
    padding: 2px 8px;
  }
}

.comboBoxWidget {
  @extend .widgets;
  width: 220px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 8px;

  & > span {
    width: 16px;
    display: inline-flex;
    height: 16px;
    color: var(--color-blue600);
  }
}
