@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/src/theme' as *;

$input-height: 30px;

.root {
  position: absolute;
  top: $toolbar-height;
  height: $toolbar-height;
  right: 0;
  padding: 5px 10px;
  width: 400px;
  border-top: 1px solid var(--PSPDFKit-SearchForm-border);
  flex-direction: row;
  display: flex;
  align-items: center;
  background-color: var(--PSPDFKit-SearchForm-background);
  border-radius: 0 0 $borderRadius-4 $borderRadius-4;
  opacity: 1;
  transition: opacity 0.4s ease 0s;
  z-index: $zIndex4;

  :global(.PSPDFKit-width-xs) & {
    width: 100%;
    border-radius: 0;
  }
}

.noToolbar {
  top: 0;
}

.searchBox {
  flex-grow: 1;
  margin-right: 10px;
  flex-direction: row;
  display: flex;
  align-items: stretch;
  height: $input-height;
}

.input {
  flex-grow: 1;
  height: $input-height;
  padding: 5px 10px;
  padding-right: 0;
  font-size: var(--PSPDFKit-fontSize-3) !important;

  // We use a very small width here so flexbox knows that there is no minimum
  // that is required. The previous flex-grow will make sure that this input
  // field gets resized to use all available space but it would not go
  // below the browser defined minimum which can break the layout on mobile
  // devices.
  width: 0;

  background: var(--PSPDFKit-SearchForm-input-background);
  color: var(--PSPDFKit-SearchForm-input-color);

  border: none;
  border-radius: $borderRadius-4 0 0 $borderRadius-4;

  &::placeholder {
    color: var(--PSPDFKit-SearchForm-input-placeholder-color);
  }

  &:focus {
    outline: none;
  }

  &::-ms-clear {
    width: 0;
    height: 0;
  }
}

.results {
  display: none;

  &:not(.resultsDisabled) {
    display: flex;
    height: $input-height;
    line-height: $input-height - 10;
    padding: 5px 10px;

    text-align: right;
    background: var(--PSPDFKit-SearchForm-results-isDisabled-background);
    color: var(--PSPDFKit-SearchForm-results-isDisabled-color);

    // Needed for the loading indicator
    min-width: 40px;
  }
}

.button {
  font-size: inherit;
  padding: 5px 10px;
  border-radius: $borderRadius-4;
  border: 0;
  cursor: pointer;
  height: $input-height;
  line-height: $input-height - 10;
  touch-action: none;
  color: var(--PSPDFKit-SearchForm-button-color);
  background: var(--PSPDFKit-SearchForm-button-background);
  text-align: center;

  transition: all $transition-duration;

  &:hover {
    background: var(--PSPDFKit-SearchForm-button-isHovered-background);
    color: var(--PSPDFKit-SearchForm-button-isHovered-color);
    border: var(--PSPDFKit-SearchForm-button-isHovered-border);
  }
}

.control {
  height: $input-height;

  display: flex;
  flex-direction: row;

  margin-left: 1px;
  border-radius: 0 $borderRadius-4 $borderRadius-4 0;
  overflow: hidden;
}

.previous,
.next {
  // These should be square.
  width: $input-height;

  background: var(--PSPDFKit-SearchForm-button-controls-background);
  color: var(--PSPDFKit-SearchForm-button-controls-color);
  border: 1px solid transparent;
  cursor: pointer;
  transition: all $transition-duration;
  padding: 0;
  touch-action: none;

  display: flex;
  align-items: center;
  justify-content: center;

  &:focus-visible {
    transition: none;
    border: 2px solid #5e5ceb;
    border-radius: $borderRadius-4;
  }

  .icon {
    width: 12px;
    height: 12px;
    fill: var(--PSPDFKit-SearchForm-button-controls-color);

    /* stylelint-disable-next-line selector-max-specificity */
    svg {
      width: 100%;
      height: 100%;
    }
  }

  .iconLeft,
  .iconRight {
    display: flex;
    justify-content: center;
  }

  &:hover {
    background: var(--PSPDFKit-SearchForm-button-controls-isHovered-background);
  }

  &:disabled {
    pointer-events: none;

    /* stylelint-disable-next-line selector-max-specificity */
    .icon {
      fill: var(--PSPDFKit-SearchForm-button-controls-isDisabled-color);
    }
  }

  &:focus {
    outline: none;
  }

  &:last-child {
    border-left: 1px solid var(--PSPDFKit-SearchForm-button-lasChild-border);

    /* stylelint-disable selector-max-specificity */
    &:focus-visible {
      border-left: 2px solid #5e5ceb;
      border-radius: $borderRadius-4;
    }
  }
}
