/**
 * Custom var definitions for the light theme.
 *
 * This file is imported by the main light theme index.scss file,
 * and by the PostCSS loader that resolves custom CSS properties.
 */

/* stylelint-disable no-duplicate-selectors */
:root {
  /* stylelint-disable */
  --PSPDFKit-shadow-color: var(--color-coolGrey800_alpha05);
  /* stylelint-enable */
}

:root {
  /* @PSPDFKit-define App */
  --PSPDFKit-App-background: var(--color-coolGrey37);
}

:root {
  /* @PSPDFKit-define Viewport */

  --PSPDFKit-Viewport-background: var(--PSPDFKit-App-background);
}

:root {
  /* @PSPDFKit-define Button */
  --PSPDFKit-Button-isDisabled-opacity: 0.3;
  --PSPDFKit-Button--default-backgroundColor: var(--color-coolGrey50);
  --PSPDFKit-Button--default-borderColor: var(--color-coolGrey100);
  --PSPDFKit-Button--default-color: var(--color-coolGrey800);
  --PSPDFKit-Button--default-isHovered-backgroundColor: var(--color-coolGrey50);
  --PSPDFKit-Button--default-isHovered-borderColor: var(--color-coolGrey400_alpha05);
  --PSPDFKit-Button--default-isHovered-color: var(--color-coolGrey900);
  --PSPDFKit-Button--default-isActive-backgroundColor: var(--color-coolGrey100);
  --PSPDFKit-Button--default-isActive-borderColor: var(--color-coolGrey200);
  --PSPDFKit-Button--default-isActive-color: var(--color-coolGrey700);

  /** TODO:I am not sure about the colors for the disabled buttons check with Samo **/
  --PSPDFKit-Button--default-isDisabled-backgroundColor: var(--color-coolGrey50);
  --PSPDFKit-Button--default-isDisabled-borderColor: var(--color-coolGrey100);
  --PSPDFKit-Button--default-isDisabled-color: var(--color-coolGrey800);
  --PSPDFKit-Button--primary-backgroundColor: var(--color-blue600);
  --PSPDFKit-Button--primary-border: var(--color-blue700);
  --PSPDFKit-Button--primary-color: var(--color-white);
  --PSPDFKit-Button--primary-isHovered-backgroundColor: var(--color-blue600);
  --PSPDFKit-Button--primary-isHovered-borderColor: var(--color-blue900);
  --PSPDFKit-Button--primary-isHovered-color: var(--color-white);
  --PSPDFKit-Button--primary-isActive-backgroundColor: var(--color-blue700);
  --PSPDFKit-Button--primary-isActive-borderColor: var(--color-blue800);
  --PSPDFKit-Button--primary-isActive-color: var(--color-white);

  /** TODO:I am not sure about the colors for the disabled buttons check with Samo **/
  --PSPDFKit-Button--primary-isDisabled-backgroundColor: var(--color-blue600);
  --PSPDFKit-Button--primary-isDisabled-borderColor: var(--color-blue700);
  --PSPDFKit-Button--primary-isDisabled-color: var(--color-white);
  --PSPDFKit-Button--primary-inverted-color: var(--PSPDFKit-Button--primary-backgroundColor);
  --PSPDFKit-Button--danger-backgroundColor: var(--color-red50);
  --PSPDFKit-Button--danger-borderColor: var(--color-red400);
  --PSPDFKit-Button--danger-color: var(--color-red600);
  --PSPDFKit-Button--danger-isHovered-backgroundColor: var(--color-red600);
  --PSPDFKit-Button--danger-isHovered-borderColor: var(--color-red800);
  --PSPDFKit-Button--danger-isHovered-color: var(--color-white);

  /** TODO:I am not sure about the colors for the disabled buttons check with Samo **/
  --PSPDFKit-Button--danger-isDisabled-backgroundColor: var(--color-red100);
  --PSPDFKit-Button--danger-isDisabled-borderColor: var(--color-red400);
  --PSPDFKit-Button--danger-isDisabled-color: var(--color-red800);
}

