/* TextInput */

/** @PSPDFKit-define TextInput **/

/* shared */

.default,
.error,
.success {
  border: 1px solid;
  border-radius: $PSPDFKit-TextInput-borderRadius;
  font-weight: bold;
  padding: calc(var(--PSPDFKit-spacing-3) / 2) var(--PSPDFKit-spacing-3);
  width: 100%;
  touch-action: none;
  transition: all 100ms;
  display: block;
}

.default:focus,
.default:active,
.error:focus,
.error:active,
.success:focus,
.success:active {
  outline: none;
}

.default:disabled,
.error:active,
.success:disabled {
  opacity: $PSPDFKit-TextInput-disabled-opacity;
}

/* default */

.default {
  background-color: var(--PSPDFKit-TextInput--default-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--default-borderColor);
  color: var(--PSPDFKit-TextInput--default-color);

  &::placeholder {
    color: var(--PSPDFKit-TextInput--default-placeholder-color);
  }
}

.default:focus,
.default:active {
  background: var(--PSPDFKit-TextInput--default-isFocused-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--default-isFocused-borderColor);
  color: var(--PSPDFKit-TextInput--default-isFocused-color);
}

/* error */

.error {
  background-color: var(--PSPDFKit-TextInput--error-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--error-borderColor);
  color: var(--PSPDFKit-TextInput--error-color);

  &::placeholder {
    color: var(--PSPDFKit-TextInput--error-placeholder-color);
  }
}

.error:focus,
.error:active {
  background: var(--PSPDFKit-TextInput--error-isFocused-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--error-isFocused-borderColor);
  color: var(--PSPDFKit-TextInput--error-isFocused-color);
}

/* success */

.success {
  background-color: var(--PSPDFKit-TextInput--success-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--success-borderColor);
  color: var(--PSPDFKit-TextInput--success-color);

  &::placeholder {
    color: var(--PSPDFKit-TextInput--success-placeholder-color);
  }
}

.success:focus,
.success:active {
  background: var(--PSPDFKit-TextInput--success-isFocused-backgroundColor);
  border-color: var(--PSPDFKit-TextInput--success-isFocused-borderColor);
  color: var(--PSPDFKit-TextInput--success-isFocused-color);
}
