@use '@/styles/config/variables' as *;

.selectionRect {
  fill: transparent;
  shape-rendering: geometricPrecision;
  cursor: pointer;
  pointer-events: fill;

  &.isDragging {
    cursor: grabbing;
  }
}

.draggingRect {
  fill: transparent;
  stroke: $selection-rect-color;
  shape-rendering: geometricPrecision;
  cursor: pointer;
  pointer-events: fill;

  &.isDragging {
    cursor: grabbing;
  }
}

.resizeAnchor {
  fill: $resize-anchor-color;
  stroke: $selection-rect-stroke;
  pointer-events: fill;
  // By default show the move cursor when we don't have the direction (e.g. LineAnnotations)
  cursor: move;

  &.nwseCursor {
    cursor: nwse-resize;
  }

  &.neswCursor {
    cursor: nesw-resize;
  }

  &.ewCursor {
    cursor: ew-resize;
  }

  &.nsCursor {
    cursor: ns-resize;
  }
}

.svg {
  user-select: none;
  pointer-events: none;
  position: absolute;
  touch-action: none;
}
