@use '@/styles/config/variables' as *;

.wrapper {
  padding-top: 35px;
  height: $es-wrapper-height;
  width: $es-signature-area-width;
  margin: 0 auto;

  @media (max-width: 660px) {
    width: $es-signature-area-width-screens-md;
  }

  @media (max-width: $screen-xs) {
    width: $es-wrapper-width-screen-xs;
    height: $es-wrapper-height-screen-xs;
  }
}

.colorPickerContainer {
  // position the colorPicker aligned at the end of the signature area
  display: flex;
  justify-content: flex-end;
  width: $es-signature-area-width;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: var(--PSPDFKit-spacing-3);

  @media (max-width: 660px) {
    width: $es-signature-area-width-screens-md;
  }

  @media (max-width: $screen-xs) {
    width: $es-wrapper-width-screen-xs;
    padding-top: 15px;
  }
}

.canvas {
  border: 1px dashed var(--PSPDFKit-DrawingTab-canvas-borderColor);
  border-radius: $es-border-radius;

  // This padding is needed to allow user to be able to draw
  // in the canvas, if the padding is less the user cannot draw in a
  // portion of the canvas. If it's more the user can draw outside the canvas
  padding-bottom: 40.8%;

  @media (max-width: $screen-xs) {
    width: $es-signature-area-width-screens-xs;
    // Same as above
    padding-bottom: 38.8%;
  }
}

.editorCanvasContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: $screen-xs) {
    height: $es-signature-area-height-screens-xs;
  }
}

.signHere {
  right: auto;
  left: 0;
  color: var(--PSPDFKit-DrawingTab-signHere-color);
}

.deleteSignature {
  color: var(--PSPDFKit-DrawingTab-deleteSignature-color) !important;
  border: none;
  padding: 0;
}

.signHere,
.deleteSignature {
  position: inherit;
  top: auto;
  margin-top: 2em;
  width: 100%;
  text-align: center;
  transition: opacity 0.2s linear, visibility 0.2s;
}

.actionContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.actionContainer > .deleteSignature {
  width: auto;
}
