@use '@/styles/config/variables' as *;

.root {
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  z-index: $zindex-annotation-toolbar;

  // Prevents sidebar component scrollbar to appear on top of the annotation toolbar.
  //It doesn't fix it on Safari
  -webkit-backface-visibility: hidden; /* stylelint-disable-line */
}

.stickToBottom {
  bottom: $toolbar-height;
}
