.modal {
  padding: 0;
  background: transparent;
}

.fallback {
  display: flex;
  flex-direction: column;

  width: calc(100vw - 120px);
  height: calc(100vh - 64px);

  max-width: 640px;
  max-height: 320px;

  &PagesView {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    background: #f0f3f9;
  }
}
