@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

/** @PSPDFKit-define DocumentComparisonToolbar **/

$point-padding: 24px;

.toolbarReferencePoints {
  display: flex;
  flex-direction: row;
  opacity: 0.5;
  padding-right: $point-padding;

  .documentTabActive & {
    opacity: 1;
  }

  &.toolbarReferencePointsSet {
    opacity: 1;
  }
}

.referencePointParent {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.documentText {
  padding: 0 $point-padding;
  opacity: 0.5;

  &.mobileText {
    padding: 0 8px;
  }
}

.documentTabActiveText {
  opacity: 1;
}

.separator {
  width: $point-padding;
  height: 1px;
  background-color: var(--PSPDFKit-DocumentComparisonToolbar-separator-background);
  margin: 0 4px;
}

.toolbarReferencePoint {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.documentTab {
  display: flex;
  flex-direction: row;
  color: var(--PSPDFKit-DocumentComparisonToolbar-label-color);
  border-right: 1px solid var(--PSPDFKit-DocumentComparisonToolbar-tab-border);
  justify-content: space-between;
  height: 100%;
  align-items: center;
  cursor: pointer;

  .toolbarReferencePointMobile {
    overflow: hidden;

    /*
      this max width corresponds to the width of the svg container + 1px (to prevent cutoffs)
      this is applied to the inactive tab of in the manual point selection
    */
    max-width: 21px;

    /* stylelint-disable-next-line selector-max-specificity */
    &.second {
      /* stylelint-disable-next-line  */
      max-width: 0px;
    }
  }
}

.checkMarkSvg {
  height: 15px;
  width: 15px;

  svg {
    stroke-width: 3px;
  }
}

.checkMark {
  circle {
    fill: var(--PSPDFKit-DocumentComparisonToolbar-checkMark-fill);
  }

  path {
    stroke: var(--PSPDFKit-DocumentComparisonToolbar-checkMark-stroke);
  }
}

.documentCheckMark {
  circle {
    fill: var(--PSPDFKit-DocumentComparisonToolbar-documentCheckMark-fill);
  }

  path {
    stroke: var(--PSPDFKit-DocumentComparisonToolbar-documentCheckMark-stroke);
  }
}

.referencePoint {
  circle {
    fill: var(--PSPDFKit-DocumentComparisonToolbar-referencePoint-fill);
  }

  text {
    stroke: var(--PSPDFKit-DocumentComparisonToolbar-referencePointText-stroke);
    fill: var(--PSPDFKit-DocumentComparisonToolbar-referencePointText-fill);
  }
}

.referencePointSet {
  circle {
    fill: var(--PSPDFKit-DocumentComparisonToolbar-referencePointSet-fill);
  }

  text {
    stroke: var(--PSPDFKit-DocumentComparisonToolbar-referencePointSetText-stroke);
    fill: var(--PSPDFKit-DocumentComparisonToolbar-referencePointSetText-fill);
  }
}

.autoCompareTab {
  cursor: pointer;
  user-select: none;

  &.mobileTab {
    padding-right: 2px;
  }
}

.resultTab {
  padding-right: 3px;
}

.documentTabActive {
  background-color: var(--PSPDFKit-DocumentComparisonToolbar-tab-active-background);

  .toolbarReferencePointMobile {
    max-width: initial;

    /* stylelint-disable-next-line selector-max-specificity */
    &.second {
      max-width: initial;
    }
  }
}

.root {
  background: var(--PSPDFKit-DocumentComparisonToolbar-background);
  font-size: var(--PSPDFKit-fontSize-3);
  position: absolute;
  width: 100%;
  z-index: 1;
}

//
.content {
  user-select: none;
  display: flex;
  flex: 2;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: $toolbar-height;
  box-shadow: 0 1px 2px rgba($black, 0.1);
  border-bottom: 1px solid var(--PSPDFKit-DocumentComparisonToolbar-border);

  .left,
  .right {
    display: flex;
    flex-grow: 1;
    align-items: center;
    height: 44px;
    width: 100%;
    justify-content: space-between;
  }

  .right {
    justify-content: flex-start;
    flex-direction: row-reverse;
  }

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    flex: 1;
    flex-direction: row-reverse;

    /* stylelint-disable-next-line selector-max-specificity */
    .left {
      height: 44px;
      align-items: center;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    .right {
      justify-content: flex-start;
      flex-direction: row;
    }
  }

  &.mobileTextMarkup {
    flex-direction: row;
  }
}

.stickToBottom {
  bottom: $toolbar-height;

  & .content {
    box-shadow: 0 -1px 2px rgba($black, 0.1);
    border-top: 1px solid var(--PSPDFKit-DocumentComparisonToolbar-border);
    border-bottom: none;
  }
}

.controlButtonContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tabsContainer {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 100%;
}

.buttonReset,
.buttonExport,
.buttonAlignDocuments {
  color: var(--PSPDFKit-DocumentComparisonToolbar-label-color);
  padding: 5px 16px;
  border: 1px solid var(--PSPDFKit-DocumentComparisonToolbar-button-border);
  border-radius: 3px;
  margin: 0 5px;
  cursor: pointer;
  pointer-events: none;
  opacity: 0.25;

  &.mobileButton {
    padding: 6px 10px;
  }

  &Active {
    pointer-events: all;
    opacity: 1;
  }
}

.buttonAlignDocuments {
  color: var(--PSPDFKit-DocumentComparisonToolbar-highlight-button-color);
  background-color: var(--PSPDFKit-DocumentComparisonToolbar-highlight-button-background);
  opacity: 1;
  pointer-events: all;
}

.form {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
