@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

/** @PSPDFKit-define AnnotationToolbar **/

.root {
  background: var(--PSPDFKit-AnnotationToolbar-background);
  font-size: var(--PSPDFKit-fontSize-3);
}

.expanded {
  width: 100%;
}

.content {
  user-select: none;
  display: flex;
  flex: 2;
  flex-direction: row;
  align-items: center;
  height: $toolbar-height;
  box-shadow: 0 1px 2px rgba($black, 0.1);
  border-bottom: 1px solid var(--PSPDFKit-AnnotationToolbar-border);

  .left,
  .right {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  .right {
    justify-content: flex-start;
    flex-direction: row-reverse;
  }

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    flex: 1;
    flex-direction: row-reverse;

    /* stylelint-disable-next-line selector-max-specificity */
    .left {
      justify-content: flex-end;
      flex: initial;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    .right {
      justify-content: flex-start;
      flex-direction: row;
    }
  }

  &.mobileTextMarkup {
    flex-direction: row;
  }
}

.stickToBottom .content {
  box-shadow: 0 -1px 2px rgba($black, 0.1);
  border-top: 1px solid var(--PSPDFKit-AnnotationToolbar-border);
  border-bottom: none;
}

.fixedHeight {
  align-items: center;
  height: $toolbar-height;
}

.control {
  min-height: $toolbar-height;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    margin-right: 6px;
  }

  // IE11 flex min-height bug workaround
  // Bug:
  /* stylelint-disable-next-line */
  // https://connect.microsoft.com/IE/feedback/details/802625/min-height-and-flexbox-flex-direction-column-dont-work-together-in-ie-10-11-preview
  // Workaround: https://github.com/philipwalton/flexbugs/issues/231#issuecomment-362790042
  :global(.PSPDFKit-browser-engine-trident) & {
    /* stylelint-disable-next-line selector-max-specificity */
    &:after {
      content: '';
      min-height: inherit;
      font-size: 0;
    }
  }
}

.selectBox {
  height: $annotation-toolbar-button-height;
}

.textFormatControls button {
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Needed because of IE11
// See https://github.com/philipwalton/flexbugs#3-min-height-on-a-flex-container-wont-apply-to-its-flex-items

.controlWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; // Needed by IE11 to make the flex block wrap.
}

.controlWrapperAutoWidth {
  display: flex;
  flex-direction: column;
}

.separator {
  margin: 0 8px;

  &:last-child {
    display: none;
  }

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }
}

.form {
  display: flex;
  flex: 1;

  .formGroup {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;

    /* stylelint-disable-next-line selector-max-specificity */
    .button:not(.rotateButton) {
      cursor: default;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-width-xs) &,
    :global(.PSPDFKit-width-sm) &,
    :global(.PSPDFKit-width-md) & {
      /* stylelint-disable-next-line selector-max-specificity */
      .control {
        display: none;
      }

      /* stylelint-disable-next-line selector-max-specificity */
      .button {
        cursor: pointer;
      }
    }
  }

  .iconOnlyMobile {
    display: none;

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-width-xs) &,
    :global(.PSPDFKit-width-md) &,
    :global(.PSPDFKit-width-sm) & {
      display: block;
    }
  }

  .control {
    display: flex;
    align-items: center;
    line-height: 0;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

.blendModeSelect {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }

  min-width: 100px;
  height: 100%;
  display: flex;
  align-items: center;

  button {
    height: 32px;
  }
}

.fontFamilySelect,
.fontSizeSelect {
  display: flex;
  align-items: center;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }
  height: $annotation-toolbar-button-height;
  margin-right: var(--PSPDFKit-spacing-6);
}

.startLineCapSelect,
.endLineCapSelect,
.strokeDashArraySelect,
.colorSelect {
  display: flex;
  align-items: center;
}

.startLineCapSelect,
.endLineCapSelect,
.strokeDashArraySelect {
  height: $annotation-toolbar-button-height;

  line,
  polyline,
  polygon,
  rect,
  path,
  circle {
    stroke: var(--PSPDFKit-AnnotationToolbar-startLineCapSelect-circle-stroke);
  }

  button {
    height: $annotation-toolbar-button-height;
  }
}

.startLineCapSelect,
.endLineCapSelect {
  width: 64px;
}

.startLineCapSelect {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    left: 0;
    right: auto !important;
  }
}

.strokeDashArraySelect {
  width: 96px;
}

.colorSelect {
  height: $annotation-toolbar-button-height;

  &:hover,
  &[data-focus-within] {
    background: transparent;
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background: transparent;
  }
}

//var(--PSPDFKit-SliderDropdownComponent-button-isHovered-color);
.colorSelectButton {
  width: 55px;
  height: $annotation-toolbar-button-height;
  margin-right: var(--PSPDFKit-spacing-6);
  display: flex;
  align-items: center;
  background: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-background);
  border: 1px var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-border) solid;
  cursor: pointer;
  // border-radius: $border-radius-sm;
  padding: var(--PSPDFKit-spacing-7);
  justify-content: space-between;
  border-radius: 3px;
  color: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-color);

  span {
    fill: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-color);
  }

  &:hover,
  &[data-focus-within] {
    background: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-background);
    /* stylelint-disable-next-line selector-max-specificity */
    span {
      fill: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-isHovered-color);
    }
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-background);
    /* stylelint-disable-next-line selector-max-specificity */
    span {
      fill: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-isHovered-color);
    }
  }
}

// This is a temporary fix which will reduce the opacity slider's size in the text annotation
// toolbar to make enough room to fit everything into the non-responsive mode.
// @FIXME(philipp): Consider making this flexible using flex box.

.textAnnotationOpacitySlider {
  @media (max-width: 1080px) and (min-width: $screen-md) {
    width: 100px;
  }
}

.button {
  color: var(--PSPDFKit-AnnotationToolbar-button-color);
  border-left: 1px solid var(--PSPDFKit-AnnotationToolbar-button-border) !important;
  flex-shrink: 0;

  &.deleteButton,
  &.annotationNoteButton {
    border-left: 0 !important;
    border-right: 1px solid var(--PSPDFKit-AnnotationToolbar-button-isDeselected-border) !important;

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-width-xs) &,
    :global(.PSPDFKit-width-sm) &,
    :global(.PSPDFKit-width-md) & {
      border-left: 1px solid var(--PSPDFKit-AnnotationToolbar-button-isDeselected-border) !important;
      border-right: 0 !important;
    }
  }

  &.buttonNoBorder {
    border-left: 0 !important;
    border-right: 0 !important;

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-width-xs) &,
    :global(.PSPDFKit-width-sm) &,
    :global(.PSPDFKit-width-md) & {
      border-left: 1px solid var(--PSPDFKit-AnnotationToolbar-button-border) !important;
    }
  }

  /* stylelint-disable-next-line */
  &:not(.annotationNoteButton):not(.deleteButton):not(.noteIconLabel):not(.rotateButton):not(.applyRedactionsButton):not(.formDesignerButton) {
    /* stylelint-disable-next-line */
    &:hover,
    &:focus,
    &:active,
    &.isActive {
      /* stylelint-disable-next-line selector-max-specificity */
      :global(.PSPDFKit-width-lg) &,
      :global(.PSPDFKit-width-xl) & {
        background: transparent !important;
      }
    }
  }

  &:hover,
  &:focus,
  &:active,
  &.isActive {
    background: var(--PSPDFKit-AnnotationToolbar-button-isHovered-background);
  }
}

.disableEditing,
.disabledButton {
  opacity: 0.2;
  pointer-events: none;
  cursor: not-allowed;
}

.icon {
  display: flex !important;
  border: 0 !important;
  pointer-events: auto !important;
  color: var(--PSPDFKit-AnnotationToolbar-icon-color) !important;
}

.noteIconLabel {
  min-width: $toolbar-height;
  height: $toolbar-height;
  padding: 0 11px;
  cursor: pointer !important;

  &:hover,
  &:active,
  &.isActive {
    background: var(--PSPDFKit-AnnotationToolbar-button-isHovered-background);
  }
}

.focusRing {
  outline: 2px solid var(--PSPDFKit-AnnotationToolbar-button-outline);
}

/* stylelint-disable-next-line selector-max-specificity */
.formGroup:first-child .button:first-child {
  border-left: none !important;
}

.fontFamily {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: normal;
  max-width: 110px;
}

.dropdownMenu {
  box-shadow: none;
  background: var(--PSPDFKit-AnnotationToolbar-dropdownMenu-button-background);
  padding: 0;
}

.colorPickerDropdownMenu {
  display: flex;
  flex-flow: row wrap;
  box-sizing: content-box;
  padding: 10px;
}

.colorItem {
  min-width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  border-radius: 5px;
}

.colorDeleteButton {
  display: none;
  position: absolute;
  background: transparent;
  top: -7px;
  right: -6px;
  width: 16px;
  height: 16px;
  border: none;
  padding: 0;
  color: var(--PSPDFKit-AnnotationToolbar-ColorDropdown-customColor-deleteButton-color);

  @media (max-width: $screen-md) and (hover: none) {
    display: flex;
    width: 30px;
    height: 34px;
    top: -1px;
    right: -30px;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    z-index: 1;

    svg {
      width: 24px;
      height: 24px;
    }
  }
}

.colorItemContainer {
  position: relative;

  @media (max-width: $screen-md) {
    &:hover {
      background-color: var(
        --PSPDFKit-AnnotationToolbar-ColorDropdown-colorItemContainer-isFocused-background
      );
    }
  }
}

.deleteButtonIsVisibleInTouchDevices {
  display: flex;
}

/* stylelint-disable-next-line selector-max-specificity */
.colorItemContainer:hover .colorDeleteButton {
  display: block;
}

.colorSvg {
  width: 20px;
  height: 20px;
}

.lineCapsDashArrayMenu {
  padding: 10px;
  display: flex;
  align-items: center;
  stroke: var(--PSPDFKit-AnnotationToolbar-startLineCapSelect-circle-stroke);
  stroke-width: 1px;
}

.strokeDashArrayMenu {
  padding: 10px;
  display: flex;
  align-items: center;
  stroke: var(--PSPDFKit-AnnotationToolbar-startLineCapSelect-circle-stroke);
  stroke-width: 1px;
}

.nativeDropdown {
  display: none;

  // We add the extra display rule make sure that the children
  // looks correclty in Chrome

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: block;
    align-items: center;
    margin-right: var(--PSPDFKit-spacing-6);
  }
}

.fontFamilyNativeFlexbox,
.fontSizeNativeFlexbox {
  display: flex;
  align-items: center;
}

.fontColorControl {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    align-items: center;
  }
}

.overlayTextInput {
  margin-right: 10px;
}

.overlayTextMargin {
  margin-right: 10px;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    margin-right: 0;
  }
}

.groupWrapper {
  display: inline-flex;
  align-items: center;
}

.groupDescription {
  align-self: center;
  padding: 0 11px;
  color: var(--PSPDFKit-AnnotationToolbar-label-color);
}

.overlayInput {
  :global(.PSPDFKit-width-xs) & {
    max-width: 110px;
  }
}

.radioInput {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  line-height: 0;
}

.applyRedactionsWrapper {
  display: flex;
  align-items: center;
  min-height: 44px;
  padding-right: 5px;
  padding-left: 5px;
  border-right: 1px solid var(--PSPDFKit-AnnotationToolbar-button-isDeselected-border);

  @media (max-width: $screen-md) {
    border-left: 1px solid var(--PSPDFKit-AnnotationToolbar-button-isDeselected-border);
    border-right: none;
    max-width: 70px;
  }
}

.applyRedactionsButton {
  display: flex;
  height: 28px;
  background-color: var(--PSPDFKit-AnnotationToolbar-redactions-button-background);
  color: var(--PSPDFKit-AnnotationToolbar-redactions-button-color);
  white-space: nowrap;
  border-radius: 3px;
  /* stylelint-disable-next-line
    length-zero-no-unit */
  box-shadow: 0px 0.75px 1.5px rgba(0, 0, 0, 0.15);

  &:hover,
  &:focus,
  &:active,
  &.isActive {
    background-color: var(--PSPDFKit-AnnotationToolbar-redactions-button-isHovered-background);
    color: var(--PSPDFKit-AnnotationToolbar-redactions-button-isHovered-color);
    /* stylelint-disable-next-line
    length-zero-no-unit */
    filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.15));
  }

  @media (max-width: $screen-md) {
    max-width: 60px;
    border-left: none !important;
    justify-content: flex-start;

    span {
      max-width: 60px;
      overflow-x: hidden;
    }
  }
}

/* stylelint-disable-next-line selector-max-specificity */
.button.buttonNoBorder.applyRedactionsButton {
  @media (max-width: $screen-md) {
    border-left: none !important;
  }
}

.customIconSvg {
  display: inline-flex;
}
