$tab-height: 35px;
$tab-width: 80px;
$tab-list-vertical-padding: 12px;
$tab-list-horizontal-padding: 32px;

.tabsContainer {
  background-color: var(--PSPDFKit-ElectronicSignatures-tabsContainer-background);
}
.tabsList {
  list-style: none;
  margin: 0;
  padding: $tab-list-vertical-padding $tab-list-horizontal-padding;
  display: flex;
  position: relative;
  border-top: 1px solid var(--PSPDFKit-ElectronicSignatures-tabList-border-color);
  border-bottom: 1px solid var(--PSPDFKit-ElectronicSignatures-tabList-border-color);
}

.singleTab {
  display: none;
}

.tab {
  margin-right: var(--PSPDFKit-spacing-5);
  min-width: 80px;
  height: $tab-height;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--PSPDFKit-ElectronicSignatures-tabLabel-color);
  font-weight: 400;
  cursor: pointer;

  &[aria-selected='true'] {
    color: var(--PSPDFKit-ElectronicSignatures-tabLabel-active-color);
  }
}

.activeTabIndicator {
  background-color: var(--PSPDFKit-ElectronicSignatures-tabLabel-active-color);
  height: 2px;
  position: absolute;
  transition: all 200ms ease;
  top: $tab-height + ($tab-list-vertical-padding * 2);
}

.checkbox {
  display: flex;
  align-items: center;
}

.checkboxLabel {
  margin-left: var(--PSPDFKit-spacing-6);
  color: var(--PSPDFKit-ElectronicSignatures-storeCheckLabel-color);
}

.contentArea {
  background-color: var(--PSPDFKit-ElectronicSignatures-contentArea-background);
}

.footer {
  background-color: var(--PSPDFKit-ElectronicSignatures-modal-background);
  padding: var(--PSPDFKit-spacing-3) var(--PSPDFKit-spacing-3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  border-top: 1px solid var(--PSPDFKit-ElectronicSignatures-tabList-border-color);
}
