.label {
  flex-direction: row-reverse;
  justify-content: space-between;
  padding: 2px;
  width: 100%;
}

.label::after {
  color: var(--color-coolGrey1000);
  left: 13px;
  top: 0;
  box-shadow: none;
  font-size: var(--PSPDFKit-fontSize-2);
  right: 0.3125rem;
  font-weight: 800;
}

.label::before {
  border-radius: 50%;
  margin-right: 0;
  margin-left: 0.5rem;
}

/* stylelint-disable-next-line selector-max-specificity */
.input:checked + .label::before {
  background-color: var(--color-coolGrey200);
  box-shadow: none;
  border-color: var(--color-coolGrey200);
}
