@use '@pspdfkit/ui/theme' as *;

.container {
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: var(--PSPDFKit-spacing-5);
  box-sizing: border-box;
  background-color: var(--PSPDFKit-BookmarksSidebar-background);
  border-bottom: 1px solid var(--PSPDFKit-BookmarksSidebar-border);
}

.loading {
  padding: var(--PSPDFKit-spacing-2) var(--PSPDFKit-spacing-5);
  display: flex;
  justify-content: center;
  background-color: var(--PSPDFKit-BookmarksSidebar--loading-background);
}

.heading,
.pageNumber {
  text-transform: uppercase;
  color: var(--PSPDFKit-BookmarksSidebar-heading-color);
  font-weight: normal;
  font-size: inherit;
}

.heading {
  display: flex;
  align-items: center;
  margin: var(--PSPDFKit-spacing-6) 0;
  font-weight: 400;
  text-transform: uppercase;
  font-size: var(--PSPDFKit-fontSize-3);
}

.buttonContainer {
  margin-top: var(--PSPDFKit-spacing-4);
  display: flex;
}

.pageNumber {
  color: var(--PSPDFKit-BookmarksSidebar-layout-pageNumber-color);
  position: absolute;
  right: 0;
  font-size: var(--PSPDFKit-fontSize-2);
  padding: 0.1em 0 0 0.7em;
  white-space: nowrap;
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
}

.name {
  color: var(--PSPDFKit-BookmarksSidebar-name-color);
}

.layout {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 var(--PSPDFKit-spacing-4) 0 var(--PSPDFKit-spacing-7);
  padding: var(--PSPDFKit-spacing-5) 0 var(--PSPDFKit-spacing-5) var(--PSPDFKit-spacing-5);
  cursor: pointer;
}

/* stylelint-disable selector-max-specificity */
.container div [tabindex='0'] > div,
.container div [tabindex='-1'] > div {
  border-bottom: 1px solid var(--PSPDFKit-BookmarksSidebar-layout-border);
}

.layout[data-focus-within],
.layout.selected,
[tabindex='0']:focus .layout {
  z-index: 1;

  &,
  & .pageNumber {
    color: var(--PSPDFKit-BookmarksSidebar-layout-pageNumber-color);
    fill: var(--PSPDFKit-BookmarksSidebar-layout-pageNumber-color);
  }

  & .name {
    color: var(--PSPDFKit-BookmarksSidebar-name-color);
  }
}

.layout:hover {
  color: var(--PSPDFKit-BookmarksSidebar-layout-isHovered-pageNumber-color);
}

.layout[data-focus-within] {
  z-index: 1;

  &,
  & .pageNumber {
    color: var(--PSPDFKit-BookmarksSidebar-layout-isHovered-pageNumber-color);
    fill: var(--PSPDFKit-BookmarksSidebar-layout-isHovered-pageNumber-color);
  }
}

// Keep this separate otherwise the styles above won't work
// when :focus-within is not supported.
.layout:focus-within {
  z-index: 1;

  &,
  & .pageNumber {
    color: var(--PSPDFKit-BookmarksSidebar-layout-isHovered-pageNumber-color);
    fill: var(--PSPDFKit-BookmarksSidebar-layout-isHovered-pageNumber-color);
  }
}

/* stylelint-enable selector-max-specificity */
.layout button,
.edit {
  border: 0;
  padding: 0;
  background: transparent;
  cursor: pointer;
  font-style: inherit;
  font-variant: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  // We need to break the text to not overflow in IE/Edge.
  word-break: break-all;
  text-align: inherit;
  color: inherit;
}

.edit svg {
  background: var(--PSPDFKit-BookmarksSidebar-edit-background);
  position: relative;
  width: 24px;
  height: 24px;
}

.edit {
  opacity: 0;
  width: 16px;
  height: 16px;
  flex: 0 0 16px;
  color: var(--PSPDFKit-BookmarksSidebar-edit-color);
  margin-left: var(--PSPDFKit-spacing-5);

  &:hover,
  &:focus,
  &:active,
  :hover > &,
  :focus > &,
  :active > & {
    opacity: 1;
  }
}

.editVisible {
  opacity: 1;
}

.editor {
  padding: var(--PSPDFKit-spacing-5);
  position: relative;
  z-index: 2;
  background-color: var(--PSPDFKit-BookmarksSidebar-editor-background);
}

.editor .heading {
  margin-bottom: var(--PSPDFKit-spacing-5);
}

.editorControls {
  margin-top: var(--PSPDFKit-spacing-5);
  flex-wrap: nowrap;
  overflow-y: auto;
}

.editorEditButtons {
  flex: 1;
}
