@use '@pspdfkit/ui/theme' as *;

.dropdownMenu {
  position: relative;
  top: 5px;
  margin-top: 0;
  margin-bottom: 0;
  max-width: 100vw;
}

.dropdownMenuUp {
  top: auto;
  bottom: 5px;
}

.arrow {
  position: relative;

  margin-top: 1px;
  border-width: 5px;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-top-width: 0;
  border-bottom-color: var(--PSPDFKit-LayoutConfig-arrow-border);
}

.arrowUp {
  border-top-color: var(--PSPDFKit-LayoutConfig-arrow-border);
  border-top-width: 5px;
  border-bottom-width: 0;
}

// The same as `.arrow` but one pixel underneath. This is used to create a fill color effect.
.arrowFill {
  @extend .arrow;

  position: absolute;
  top: 1px;
  left: -4px;

  border-width: 4px;
  border-style: solid;
  border-top-width: 0;
  border-bottom-color: var(--PSPDFKit-LayoutConfig-arrowFill-border);
}

.arrowFillUp {
  @extend .arrowUp;

  position: absolute;
  bottom: 2px;
  left: -4px;

  border-width: 4px;
  border-style: solid;
  border-bottom-width: 0;
  border-top-color: var(--PSPDFKit-LayoutConfig-arrowFill-border);
  color: transparent;
}

.table {
  margin: 0 10px;
  border-collapse: collapse;

  tr + tr td {
    border-top: 1px solid var(--PSPDFKit-LayoutConfig-table-border);
  }
}

.button {
  display: inline-block;
  cursor: pointer;
  touch-action: none;
}

.icon {
  margin: 5px 0;
  padding: 3px;
  border-radius: $borderRadius-5;
  display: inline-block;
}

.activeIcon {
  color: var(--PSPDFKit-LayoutConfig-icon-isActive-color);
  background-color: var(--PSPDFKit-LayoutConfig-icon-isActive-background);
}

.highlightedIcon:not(.activeIcon) {
  background-color: var(--PSPDFKit-LayoutConfig-icon-isHighlighted-background);
}
