/* @PSPDFKit-namespace UserHintComponent */

.container {
  position: fixed;
  bottom: 20px;
  right: 40px;
  width: 240px;
  background: var(--PSPDFKit-UserHintComponent-background);
  border: 1px solid var(--PSPDFKit-UserHintComponent-border);
  box-sizing: border-box;
  border-radius: 4px;
  color: var(--PSPDFKit-UserHintComponent-color);
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
}

.header {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  padding-bottom: 5px;
  align-items: center;
  max-width: 100%;
}

.hint,
.hideButton {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  text-align: left;
  max-width: 100%;
}

.hideButton {
  padding-top: 5px;
  /* stylelint-disable selector-max-specificity */
  & button {
    background-color: var(--PSPDFKit-UserHintComponent-background);
    border: none;
    color: var(--PSPDFKit-UserHintComponent-color);
    padding: 0;
    text-align: left;

    &:hover {
      background-color: var(--PSPDFKit-UserHintComponent-background);
      text-decoration: underline;
      color: var(--PSPDFKit-UserHintComponent-color);
    }
  }
}

.icon {
  padding-bottom: 5px;
}
