// Some keyboard navigation (in Downshift components, for example) does not set :focus-visible
// on [tabindex] elements, and it has to be manually set https://css-tricks.com/copy-the-browsers-native-focus-styles/
/* stylelint-disable-next-line selector-max-specificity */
.focusVisible [tabindex]:not(:focus-visible) {
  outline: 5px auto Highlight;
  outline: 5px auto -webkit-focus-ring-color;
}

// When focusing with focus(), target elements do not have their :focus-visible state set,
// so we need to set it manually
// if keyboard navigation is used: https://css-tricks.com/copy-the-browsers-native-focus-styles/
/* stylelint-disable-next-line selector-max-specificity */
.focusedFocusVisible[tabindex]:focus:not(:focus-visible) {
  outline: 5px auto Highlight;
  outline: 5px auto -webkit-focus-ring-color;
}
