@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

.root {
  user-select: none;
  display: flex;
  flex: 2;
  flex-direction: row;
  align-items: center;
  height: $toolbar-height;
  min-height: $toolbar-height;
  color: var(--PSPDFKit-Toolbar-color);
  /* stylelint-disable */
  box-shadow: 0 1px 2px var(--PSPDFKit-Toolbar-boxShadow);
  /* stylelint-enable */
  position: relative;
  font-weight: normal;
  white-space: nowrap;
  background-color: var(--PSPDFKit-Toolbar-background);
  opacity: 1;
  transition: opacity 0.4s ease 0s;
  z-index: $zindex-toolbar;
  overflow-x: auto;
  // Smooth scrolling on webkit.
  // FIXME (miguel): this is making the responsive group buttons disappear once
  // the stamps UI is shown. Temporarily disabled.
  // -webkit-overflow-scrolling: touch;
  // When there is overflow hides the scrollbar but keeps the container scrollable.
  -ms-overflow-style: none;
  // Hide the vertical scroll bar in IE11.
  overflow-y: hidden;

  .left,
  .right {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  .right {
    justify-content: flex-end;
  }

  // This is so the first button doesn't have a left border when selected.
  /* stylelint-disable selector-max-specificity */
  > :first-child,
  > :first-child .toolbarButton {
    border: 0;
  }
  /* stylelint-enable selector-max-specificity */
}

.separator {
  margin: 0 12px;

  &:last-child {
    display: none;
  }

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }
}

.buttonGroup {
  display: flex;
  align-items: center;
  justify-content: center;
}

.toolbarButton {
  border-left: 1px solid var(--PSPDFKit-Toolbar-button-border);
  flex-shrink: 0;
}

.dropdownButton {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  font: inherit;
  border-radius: $borderRadius-4;
  border: 0;
  padding-right: 24px;
  white-space: nowrap;

  // This is to preserve backwards compatibility.
  // Consider making a breaking change to get rid of the nested selector.
  /* stylelint-disable-next-line selector-max-specificity */
  &.dropdownButton {
    color: inherit;
  }
}

.dropdownButtonSelected {
  background-color: var(--PSPDFKit-Toolbar-dropdownButton-isSelected-background);
  color: var(--PSPDFKit-Toolbar-dropdownButton-isSelected-color) !important;
}

.dropdownButtonDisabled {
  background-color: var(--PSPDFKit-Toolbar-dropdownButton-isDisabled-background);
}

.dropdownButtonFocused,
.dropdownButton:hover,
.dropdownButton:focus {
  background-color: var(--PSPDFKit-Toolbar-dropdownButton-isFocused-background);
  color: var(--PSPDFKit-Toolbar-dropdownButton-isFocused-color);

  // This is to preserve backwards compatibility.
  // Consider making a breaking change to get rid of the nested selector.
  /* stylelint-disable-next-line selector-max-specificity */
  &.dropdownButton {
    color: var(--PSPDFKit-Toolbar-dropdownButton-isFocused-color);
  }
}
