@use '@/styles/config/variables' as *;

.root {
  position: relative;
  display: block;
  background: white;
  overflow: hidden;

  &.deg-0 {
    box-shadow: 3px 3px 8px rgba($black, 0.1);
  }

  &.deg-90 {
    box-shadow: 3px -3px 8px rgba($black, 0.1);
  }

  &.deg-180 {
    box-shadow: -3px -3px 8px rgba($black, 0.1);
  }

  &.deg-270 {
    box-shadow: -3px 3px 8px rgba($black, 0.1);
  }
}

.image {
  // Prevent selecting the image when selecting text.
  // Apparently unrelated to image dragging.
  user-select: none;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;

  /* stylelint-disable-next-line */
  :global(.PSPDFKit-browser-engine-edge) & {
    /* stylelint-disable-next-line selector-max-specificity */
    &::selection {
      background: transparent;
    }
  }
}

// When using text highlighter, hide the selection rect color so as not to
// mix its color with the highlight annotation color
// We have to use !important to override selection background color settings in Text.scss,
// otherwise it won't work on Firefox.
// We also set the color to transparent to avoid the selected text showing on Android.
.textHighlighterMode {
  ::selection {
    color: transparent !important;
    background: transparent !important;
  }

  :global(.PSPDFKit-browser-system-android) & {
    /* stylelint-disable-next-line selector-max-specificity */
    span::selection {
      color: transparent;
      background: transparent;
    }
  }

  :global(.PSPDFKit-browser-system-windows) & {
    /* stylelint-disable-next-line selector-max-specificity */
    ::selection {
      // In Edge/WebView sets the outside ring of the selection handle to this color
      // Otherwise it would be white and the inside it white and it's not visible on white
      // background.
      background: rgb(18 61 112 / 0.05) !important;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    span::selection {
      color: transparent;
      background: transparent;
    }
  }
}

.focusPage {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  user-select: none;
}

.hideCursor {
  cursor: none;
}
