/* Button */

.general {
  cursor: pointer;
  font: inherit;
  font-weight: normal;
}

/* shared */

.default,
.primary {
  border: 1px solid;
  border-radius: $PSPDFKit-Button-borderRadius;
  padding: calc(var(--PSPDFKit-spacing-5) / 2) var(--PSPDFKit-spacing-5);
  touch-action: none;
  transition: all 100ms;
  user-select: none;
  white-space: nowrap;
}

/* default */

.default {
  background-color: var(--PSPDFKit-Button--default-backgroundColor);
  border-color: var(--PSPDFKit-Button--default-borderColor);
  color: var(--PSPDFKit-Button--default-color);
}

.default:hover {
  background: var(--PSPDFKit-Button--default-isHovered-backgroundColor);
  border-color: var(--PSPDFKit-Button--default-isHovered-borderColor);
  color: var(--PSPDFKit-Button--default-isHovered-color);
}

.default:active,
.default:focus {
  background-color: var(--PSPDFKit-Button--default-isActive-backgroundColor);
  border-color: var(--PSPDFKit-Button--default-isActive-borderColor);
  color: var(--PSPDFKit-Button--default-isActive-color);
}

.default.disabled {
  background-color: var(--PSPDFKit-Button--default-isDisabled-backgroundColor);
  border-color: var(--PSPDFKit-Button--default-isDisabled-borderColor);
  color: var(--PSPDFKit-Button--default-isDisabled-color);
  opacity: var(--PSPDFKit-Button-isDisabled-opacity);
  cursor: default;
}

.default.inverted {
  color: var(--PSPDFKit-Button--default-color);
}

/* primary */

.primary {
  background-color: var(--PSPDFKit-Button--primary-backgroundColor);
  border-color: var(--PSPDFKit-Button--primary-border);
  color: var(--PSPDFKit-Button--primary-color);
}

.primary:hover {
  background-color: var(--PSPDFKit-Button--primary-isHovered-backgroundColor);
  border-color: var(--PSPDFKit-Button--primary-isHovered-borderColor);
  color: var(--PSPDFKit-Button--primary-color);
}

.primary:focus,
.primary:active {
  background: var(--PSPDFKit-Button--primary-isActive-backgroundColor);
  border-color: var(--PSPDFKit-Button--primary-isActive-borderColor);
  color: var(--PSPDFKit-Button--primary-isActive-color);
}

.primary.disabled {
  background-color: var(--PSPDFKit-Button--primary-isDisabled-backgroundColor);
  border-color: var(--PSPDFKit-Button--primary-isDisabled-borderColor);
  color: var(--PSPDFKit-Button--primary-isDisabled-color);
  opacity: var(--PSPDFKit-Button-isDisabled-opacity);
  cursor: default;
}

.primary.inverted {
  color: var(--PSPDFKit-Button--primary-inverted-color);
}

/* danger */

.danger {
  background-color: var(--PSPDFKit-Button--danger-backgroundColor);
  border-color: var(--PSPDFKit-Button--danger-borderColor);
  color: var(--PSPDFKit-Button--danger-color);
}

.danger:hover,
.danger:focus,
.danger:active {
  background: var(--PSPDFKit-Button--danger-isHovered-backgroundColor);
  border-color: var(--PSPDFKit-Button--danger-isHovered-borderColor);
  color: var(--PSPDFKit-Button--danger-isHovered-color);
}

.danger.disabled {
  background-color: var(--PSPDFKit-Button--danger-isDisabled-backgroundColor);
  border-color: var(--PSPDFKit-Button--danger-isDisabled-borderColor);
  color: var(--PSPDFKit-Button--danger-isDisabled-color);
  opacity: var(--PSPDFKit-Button-isDisabled-opacity);
  cursor: default;
}

/** Button looks better with border color than background color **/
.danger.inverted {
  background: var(--PSPDFKit-Button--danger-backgroundColor);
  color: var(--PSPDFKit-Button--danger-color);
  border-color: var(--PSPDFKit-Button--danger-borderColor);
}

/* stylelint-disable selector-max-specificity */
.danger.inverted:hover,
.danger.inverted:focus,
.danger.inverted:active {
  background-color: var(--PSPDFKit-Button--danger-isHovered-backgroundColor);
  border-color: var(--PSPDFKit-Button--danger-isHovered-borderColor);
  color: var(--PSPDFKit-Button--danger-isHovered-color);
}

/* stylelint-enable selector-max-specificity */

/* inverted, to be used in combination with one of the button types above */

.inverted,
.inverted:hover,
.inverted:focus,
.inverted:active {
  background-color: transparent;
}

.inverted:hover,
.inverted:focus,
.inverted:active {
  border-color: currentColor;
}

/* group, add spacing between buttons */

.group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.groupAlignStart {
  justify-content: flex-start;
}

.groupAlignEnd {
  justify-content: flex-end;
}

.groupSpacer {
  width: 1em;
  min-width: 0.25em;
}
