@use '@/styles/config/variables' as *;

.layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: default;
}

.comboBox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 0 1 auto;
  margin: 0;
  padding: 0;
  position: relative;

  &.expanded {
    z-index: $zindex-dropdown;
  }
}

.comboBoxTop {
  position: relative;
  display: block;
  width: 100%;
}

.valueBtn {
  border: 0;
  padding: 0 0.3em;
  background-color: transparent;
  box-sizing: border-box;
  text-align: left;
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin-right: 0;
  white-space: nowrap;
  font: inherit;

  /* stylelint-disable-next-line selector-max-specificity */
  &[disabled],
  &[disabled] + .arrowBtn {
    color: lighten($black, 40%);
  }
}

.arrowBtn {
  position: absolute;
  width: 0.7em;
  top: 50%;
  margin-top: -0.35em;
  right: 0.25em;
  border: none;
  color: $gray;

  svg {
    height: 0.7em;
    display: block;
    border: 0 !important;
    fill: currentColor;
  }
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: $white;
  box-sizing: border-box;
  width: 100%;
  min-width: 3em;
  outline: 2px solid darken($widget-annotation-focus-color, 40%);
}

.comboBoxInput {
  margin: 0.2em;
  padding: 0.2em;
  flex: 0 1 auto;
  min-width: 0;
  box-sizing: border-box;
  min-height: 0;
}

.comboBoxList {
  box-sizing: border-box;
  border-style: none;
  margin-top: 0.1em;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  overflow: none;
  cursor: pointer;
}

.comboBoxListItem {
  text-align: left;
  color: $gray-dark;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.comboBoxListItemSelected {
  background-color: $widget-annotation-color;
}

.comboBoxListItemActive {
  color: $white;
  background-color: darken($widget-annotation-color, 10%);
}

.empty {
  color: darken($gray-light, 25%);

  .comboBoxListItemSelected & {
    color: $gray-dark;
  }

  .comboBoxListItemActive & {
    color: $white;
  }
}
