.tile {
  user-select: none;
  position: absolute;
}

/* stylelint-disable selector-max-specificity */
.tile.tileChrome {
  // Scaled down images appear blurry in Chrome, specially in non-retina screens
  // https://bugs.chromium.org/p/chromium/issues/detail?id=562162
  img,
  canvas {
    image-rendering: -webkit-optimize-contrast; /* stylelint-disable-line */
  }

  &.crispTile img,
  &.crispTile canvas {
    image-rendering: pixelated;
  }
}

.tile.tileSafari.crispTile {
  img,
  canvas {
    image-rendering: pixelated;
  }
}

.tile.tileFirefox.crispTile {
  img,
  canvas {
    image-rendering: crisp-edges;
  }
}

.tile.tileIE11.crispTile {
  img,
  canvas {
    -ms-interpolation-mode: nearest-neighbor; /* stylelint-disable-line */
  }
}
/* stylelint-enable selector-max-specificity */

.tilePlaceholder {
  background: white;
}
