@use '@/styles/config/variables' as *;

.addIcon {
  width: 40px;
  height: 40px;
  display: block;
  cursor: pointer;
}

.addButton {
  border: none;
  background: none;
  color: inherit;
}

.dragZone {
  height: $es-signature-area-heigh;
  border: 1px dashed var(--PSPDFKit-ElectronicSignatures-imagePicker-borderColor);
  border-radius: $es-border-radius;
  font-size: var(--PSPDFKit-fontSize-3);
  width: $es-signature-area-width;
  display: flex;
  cursor: pointer;

  @media (max-width: 660px) {
    width: $es-signature-area-width-screens-md;
  }

  @media (max-width: $screen-xs) {
    width: $es-signature-area-width-screens-xs;
    height: $es-signature-area-height-screens-xs;
  }
}

.dragZoneNoLegacyEdge {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dragZoneNoSelection {
  margin-bottom: 5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.wrapper,
.dragZoneContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.wrapper {
  padding-top: 5rem;
  color: var(--PSPDFKit-ElectronicSignatures-imagePicker-color);
  height: $es-wrapper-height;

  @media (max-width: $screen-xs) {
    height: $es-wrapper-height-screen-xs;
  }
}

.imagePreview {
  max-width: 100%;
  max-height: 100%;
  min-height: 1px; // Needed for IE11
}

// Legacy Edge needs very specific properties for the divs to update correctly when changing the img.
// Otherwise the divs won't update properly.
.legacyEdgeImagePreview {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  display: inline-block;
}

.legacyEdgeImagePreview img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.draggingOver,
.hoveringOver {
  background-color: var(--PSPDFKit-ElectronicSignatures-imagePicker-draggingBackground);
  border: 1px dashed var(--PSPDFKit-ElectronicSignatures-imagePicker-draggingBorder);
}

.legend {
  margin-top: 14px;
}

.filePicker {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
}

.fileLabel {
  cursor: pointer;
  border: none;
  background: none;
  color: inherit;
  font-size: inherit;
  margin-bottom: 28px;
}

.buttonLink {
  margin-top: 2rem;
}
