@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

$signature-mq: 350px;

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: var(--PSPDFKit-spacing-3);
}

.legend {
  color: var(--PSPDFKit-ElectronicSignatures-modal-legend-color);
  font-weight: 600;
  font-size: var(--PSPDFKit-fontSize-4);
  margin-left: var(--PSPDFKit-spacing-3);
  margin-top: var(--PSPDFKit-spacing-3);
}

.footer {
  margin-top: var(--PSPDFKit-spacing-3);
  margin-bottom: var(--PSPDFKit-spacing-3);
  margin-right: var(--PSPDFKit-spacing-3);
}

.addButton {
  margin-top: var(--PSPDFKit-spacing-3);
  margin-right: var(--PSPDFKit-spacing-3);
}

.signaturePickerContainer {
  max-height: 50vh;
  overflow: auto;
  flex: 1 1 auto;

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-height: 100%;
  }
  margin-left: var(--PSPDFKit-spacing-4);
  margin-right: var(--PSPDFKit-spacing-4);
}
