.root {
  touch-action: pan-x;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: auto;
  background-color: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  border: 1px solid transparent;
  padding: 0;
}

.root:disabled,
.isDisabled {
  pointer-events: none;
  cursor: default;
}

.withLabel {
  margin-right: 11px;
}
