@import '../theme';

.radioGroup {
  margin: 0 4px;
  display: flex;
  align-items: center;
}

.iconLabel {
  cursor: pointer;
  margin: 8px 1px;
  height: 32px;
  width: 32px;
  background: var(--PSPDFKit-RadioGroup-icon-label-background);
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: 24px;
    height: 24px;
    fill: var(--PSPDFKit-RadioGroup-icon-color);
  }

  input {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    width: 1px;
    visibility: hidden;
  }

  &:first-of-type {
    border-radius: $borderRadius-5 0 0 $borderRadius-5;
    margin-right: 0;
  }

  &:last-of-type {
    border-radius: 0 $borderRadius-5 $borderRadius-5 0;
    margin-left: 0;
  }

  &[data-focus-within] {
    outline: 1px solid lighten($color-gray2, 60%);
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    outline: 1px solid lighten($color-gray2, 60%);
  }
}

.iconLabelIsActive {
  background: var(--PSPDFKit-RadioGroup-iconLabel-isActive-background);
  border-color: var(--PSPDFKit-RadioGroup-iconLabel-isActive-border);

  svg {
    color: var(--PSPDFKit-RadioGroup-iconLabel-isActive-svg-color);
    fill: var(--PSPDFKit-RadioGroup-iconLabel-isActive-svg-fill);
  }
}

.icon {
  display: flex !important;
  border: 0 !important;
  pointer-events: auto !important;
  color: var(--PSPDFKit-RadioGroup-icon-color) !important;
}

// The default outline value for IE11 is difficult to notice on some input controls, like those for text alignment.
// This style is specific for IE11 so the focus ring can be noticed.
/* stylelint-disable selector-max-specificity */
:global(.PSPDFKit-browser-engine-trident) [data-focus-within].focusedLabelFocusVisible {
  outline: 1px dotted black !important;
}
