@use '@/styles/config/variables' as *;

.container {
  pointer-events: none;
}

.wrapper {
  z-index: 1; // to render it on top of other annotations
}

.canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  user-select: none;
  cursor: crosshair;
}

.rectContainer {
  position: absolute;
  text-align: left;
}

.active {
  position: absolute;
  // We grow the active redaction annotation so the user sees which is selected.
  // Especially useful when the redaction is applied to text.
  bottom: -$active-redaction-padding;
  left: -$active-redaction-padding;
  top: -$active-redaction-padding;
  right: -$active-redaction-padding;
  border: 2px solid $selection-rect-color;
}

.selectable {
  cursor: pointer;
}

.textContainer {
  overflow: hidden;
  word-wrap: break-word; // For IE11
  overflow-wrap: break-word;
  line-height: 0;
  font-family: Helvetica, sans-serif;
}

.textSpan {
  display: inline-block;
  width: 100%;
  overflow: hidden;
}

.svg {
  user-select: none;
  pointer-events: none;
  position: absolute;
}
