@use '@pspdfkit/ui/theme' as *;

.container {
  padding: var(--PSPDFKit-spacing-7) calc(var(--PSPDFKit-spacing-7) / 2);
  padding-right: 0;
  height: 100%;
  overflow: auto;
  background-color: var(--PSPDFKit-DocumentOutline-background);
}

.empty {
  padding: var(--PSPDFKit-spacing-2) var(--PSPDFKit-spacing-6);
  display: flex;
  justify-content: center;
}

.content {
  padding-left: var(--PSPDFKit-spacing-6);
}

.controlContainer {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.controlContainerNavigationDisabled {
  cursor: default;
}

.controlContainerNoIcon {
  padding-left: 24px;
}

.control {
  display: block;
  width: 100%;
  padding: var(--PSPDFKit-spacing-5) var(--PSPDFKit-spacing-4) var(--PSPDFKit-spacing-5) 0;
  color: var(--PSPDFKit-DocumentOutline-control-color);
  border-bottom: 1px solid var(--PSPDFKit-DocumentOutline-control-border);
}

.icon {
  display: block;
  color: var(--PSPDFKit-DocumentOutline-icon-color);
  fill: var(--PSPDFKit-DocumentOutline-icon-color);
  margin-right: calc(var(--PSPDFKit-spacing-7) / 2);
  cursor: pointer;
}

.icon svg {
  width: 100%;
  height: 100%;
}

.iconExpanded {
  transform: rotateZ(90deg);
  transition: transform 0.2s ease;
}

/* stylelint-disable-next-line selector-max-specificity */
.controlContainer:hover .icon,
.iconExpanded {
  color: var(--PSPDFKit-DocumentOutline-icon-isHovered-color);
  fill: var(--PSPDFKit-DocumentOutline-icon-isHovered-color);
}

/* stylelint-disable-next-line selector-max-specificity */
div[id^='outline-control'] .controlContainerNoIcon > .control {
  padding-left: 10px;
}
