@use '@/styles/config/variables' as *;

// Thumb
$thumb-color: #fff !default;
$thumb-hover-color: #fff !default;
$thumb-radius: 100% !default;
$thumb-height: 15px !default;
$thumb-width: 15px !default;
$thumb-border-width: 0 !default;
$thumb-border-color: #000 !default;

// Track
$track-color: #999 !default;
$track-progress-color: #999 !default;
$track-progress-hover-color: #999 !default;
$track-radius: 4px !default;
$track-width: auto !default;
$track-min-width: auto !default;
$track-height: 8px !default;
$track-border-width: 1px !default;
$track-border-color: #000 !default;

@mixin track {
  width: $track-width;
  min-width: $track-min-width;
  height: $track-height;
  cursor: pointer;
}

@mixin thumb {
  box-sizing: border-box;
  border: $thumb-border-width solid $thumb-border-color;
  height: $thumb-height;
  width: $thumb-width;
  border-radius: $thumb-radius;
  background: $thumb-color;
  cursor: pointer;
  box-shadow: 0 1px 2px 1px rgba($black, 0.2);
  transition: background-color 0.2s;

  &:hover,
  &:focus,
  &:active {
    background: $thumb-hover-color;
  }
}

@mixin input-type-range {
  appearance: none;
  background: transparent;
  width: $track-width;
  min-width: $track-min-width;
  padding: 0;

  &::-webkit-slider-runnable-track {
    @include track;

    background: #4636e3;
    border-radius: $track-radius;
    border: $track-border-width solid $track-border-color;
  }

  &::-webkit-slider-thumb {
    @include thumb;

    appearance: none;
    margin-top: (-$track-border-width * 2 + $track-height) * 0.5 - $thumb-height * 0.5;
  }

  &:focus::-webkit-slider-runnable-track {
    background: #4636e3;
  }

  &::-moz-range-track {
    @include track;

    background: #4636e3;
    border-radius: $track-radius;
    border: $track-border-width solid $track-border-color;
  }

  &::-moz-range-progress {
    @include track;

    background: #4636e3;
    border-radius: $track-radius;
    border: $track-border-width solid $track-progress-color;
  }

  &::-moz-range-thumb {
    @include thumb;
  }

  &::-moz-focus-outer {
    border: 0;
  }

  &::-ms-track {
    @include track;

    background: transparent;
    border-color: transparent;
    border-width: $thumb-width 0;
    color: transparent;

    :global(.PSPDFKit-browser-engine-trident) & {
      margin-top: -2px;
      margin-bottom: -2px;
    }

    :global(.PSPDFKit-browser-engine-edge) & {
      margin-top: -8px;
      margin-bottom: -8px;
    }
  }

  &::-ms-fill-lower {
    background: #4636e3;
    border: $track-border-width solid #4636e3;
    border-radius: $track-radius * 2;
  }

  &::-ms-fill-upper {
    background: #eee;
    border: $track-border-width solid #eee;
    border-radius: $track-radius * 2;
  }

  &::-ms-thumb {
    @include thumb;

    background: white;
    // Adjust position in Edge
    transform: translateY($thumb-border-width);

    // Reset the thumb position in IE11
    :global(.PSPDFKit-browser-engine-trident) & {
      transform: none;
    }
  }
}
