@use '@pspdfkit/ui/theme' as *;
@use '@/styles/config/variables' as *;

.sidebar {
  position: relative;
  word-break: break-word;
  overflow-wrap: break-word;
  background-color: var(--PSPDFKit-Sidebar-background);
  height: 100%;
  z-index: $zindex-sidebar;
  font-size: var(--PSPDFKit-fontSize-3);
}

.sidebarHeader {
  display: flex;
  background: var(--PSPDFKit-Sidebar-header-background);
  flex-wrap: wrap;
  padding: var(--PSPDFKit-spacing-6) var(--PSPDFKit-spacing-4);
  text-align: right;
  flex-direction: column;
}

.dragger {
  cursor: col-resize;
  position: absolute;
  top: 0;
  bottom: 0;
  user-select: none;
  overflow: hidden;
  background-color: var(--PSPDFKit-Sidebar-dragger-background);
  border-left: 1px solid var(--PSPDFKit-Sidebar-dragger-border);
}

.draggerHandle {
  position: fixed;
  width: 9px;
  top: 50%;
  border: 1px solid var(--PSPDFKit-Sidebar-draggerHandle-border);
  border-left: none;
  background: var(--PSPDFKit-Sidebar-draggerHandle-background);
  height: 32px;
  display: block;
  border-radius: 3px;
}

.draggerHandle:before,
.draggerHandle:after {
  content: '';
  width: 1px;
  background-color: var(--PSPDFKit-Sidebar-dragger-pseudo-background);
  height: 20px;
  margin-top: -15px;
  position: absolute;
  top: 64%;
  left: 50%;
}

.draggerHandle:before {
  margin-left: -2px;
}

.draggerHandle:after {
  margin-left: 1px;
}

@media (max-width: 768px) {
  .draggerHandle {
    display: none;
  }
}

.container {
  overflow: auto;
  height: 100%;
}
