@use '@/styles/config/variables' as *;

.widget {
  background-color: rgba($widget-annotation-color, 0.9);
  border: 1px solid desaturate(darken($widget-annotation-color, 25%), 25%);
  box-sizing: border-box;
  display: block;
  font-family: Helvetica, sans-serif;
  margin: 0;

  &:hover {
    outline: 1px solid desaturate(darken($widget-annotation-color, 25%), 25%);
  }
}

.readOnly {
  &,
  &:hover {
    cursor: default;
    background-color: rgba($widget-read-only-color, 0.9);
    border-color: darken($widget-read-only-color, 15%);
    color: gray;
  }
}

.focusedWidget {
  /* stylelint-disable-next-line selector-max-specificity */
  :global(.PSPDFKit-browser-engine-gecko.PSPDFKit-browser-system-windows) &:focus-visible {
    outline: 5px auto Highlight;
  }

  &,
  &:hover {
    // Overwriting any custom styling when editing the annotation. If we would not do that
    color: $widget-annotation-focus-color;
    background-color: $widget-annotation-focus-background-color;
    border-color: darken($widget-annotation-focus-background-color, 40%);
  }
}

.onFocus {
  &:focus {
    @extend .focusedWidget;
  }
}

.combNoPaddingLeftOnFocus:focus {
  padding-left: 0 !important;
}

.flexText {
  display: flex;
  flex-direction: column;
}
