@use '@/styles/config/variables' as *;

.annotationRect {
  position: absolute;
}

.highlight:focus,
.activeTextMarkup {
  outline: 2px solid $custom-text-selection-color !important;
  outline-offset: 2px;
}

.active {
  position: absolute;
  // We grow the active textmarkup annotation so the user sees which textmarkup annotation is selected.
  bottom: $active-textmarkup-padding;
  left: $active-textmarkup-padding;
  top: $active-textmarkup-padding;
  right: $active-textmarkup-padding;
  background: rgba(#fff, 0.5);
  border-radius: 3px;
}

.highlight {
  .markup {
    cursor: pointer;
  }
}

.strikeout {
  .markup {
    position: absolute;
    background-color: #900;
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    content: '';
  }

  .active {
    opacity: 0.1;
  }
}

.underline {
  .markup {
    position: absolute;
    background-color: #900;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    content: '';
  }

  .active {
    opacity: 0.1;
  }
}

.squiggle {
  .markup {
    position: absolute;
    background: none !important;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
  }

  .active {
    opacity: 0.1;
  }
}
