@use '@/styles/config/variables' as *;

.layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.annotation {
  position: absolute;
  overflow: hidden;
}

.editorWrapper {
  padding: 0;
  margin: 0;

  &:focus {
    outline: none;
  }
}

.editor {
  position: absolute;
  width: auto;
  height: auto;
  background: inherit;
  overflow: hidden;
  cursor: text;

  // This is necessary otherwise `contenteditable` won't work in Safari
  // when `user-select` is set to `none`
  // Also it must be `text` otherwise `all` won't work in Firefox.
  user-select: text !important;

  * {
    margin: 0;
    padding: 0;
  }

  &:focus {
    outline: none;
  }
}
