@use 'sass:math';
@use '@/styles/config/variables' as *;
// stylelint-disable-next-line
@use '@pspdfkit/ui/theme' as *;

$stamp-mq: 350px;

/* stamp specific modal styles */

// These values are also used in StampPickerComponent.js. If you modify them,
// please update it there too.
$picker_max_width: 640px;
$editor_max_width: 480px;

// percentages (relative to picker width)
$item_width_total: 46.25;
$item_width_total_desktop: 30;

// percentages (relative to picker padded content)
$item_width: 47.85;
$item_width_desktop: 31.03;

$items_per_row: 2;
$items_per_row_desktop: 3;

// percentages (relative to picker width)
$item_margin_total: math.div((100 - ($item_width_total * $items_per_row)), ($items_per_row * 2));
$item_margin_total_desktop: math.div(
  (100 - ($item_width_total_desktop * $items_per_row_desktop)),
  ($items_per_row_desktop * 2)
);

// percentages (relative to picker padded content)
$item_margin: math.div((100 - ($item_width * $items_per_row)), ($items_per_row * 2));
$item_margin_desktop: math.div(
  (100 - ($item_width_desktop * $items_per_row_desktop)),
  ($items_per_row_desktop * 2)
);

.modal {
  max-width: $picker_max_width;
}

.editorModal {
  max-width: $editor_max_width;
}

.modal,
.editorModal {
  width: 100%;
  max-height: 100vh;
  padding: 0;

  @media (max-height: 455px) {
    height: 90%;
  }

  @media (max-height: 300px) {
    height: 100%;
  }

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-width: 100%;
    height: 100%;
    border-radius: 0;
  }
}

.previewContainerBlock {
  margin-left: calc(2 * var(--PSPDFKit-spacing-5));
  margin-right: calc(2 * var(--PSPDFKit-spacing-5));

  @media (max-width: $screen-md) {
    margin-left: var(--PSPDFKit-spacing-5);
    margin-right: var(--PSPDFKit-spacing-5);
  }
}

.stampModalSection {
  position: relative;
  // Override negative margin from packages/ui/src/modal/index.scss
  margin: 0 !important;
  padding: var(--PSPDFKit-spacing-3);

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $screen-md) {
    padding: var(--PSPDFKit-spacing-5);
  }
}

/* view is basically the container for the entire feature
and it is rendered inside of a modal */

.viewRoot {
  color: var(--PSPDFKit-StampViewComponent-header-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  @media (orientation: landscape) and (max-width: $screen-md), (max-height: $screen-xs) {
    height: auto;
  }
}

.viewStampsPicker {
  height: auto;
  max-height: 90vh;

  @media (orientation: landscape) and (max-width: $screen-md), (max-height: $screen-xs) {
    max-height: none;
  }
}

/* layout */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--PSPDFKit-spacing-3);
  border-style: solid;
  border-color: var(--PSPDFKit-StampViewComponent-header-border);
  border-width: 0;
  flex-direction: row;
  flex-wrap: wrap;
  z-index: 1;

  @media (max-width: $screen-md) {
    padding: var(--PSPDFKit-spacing-5);
  }
}

.footer {
  margin-top: var(--PSPDFKit-spacing-3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.colorPicker {
  font-size: var(--PSPDFKit-fontSize-6);
  justify-content: space-between;
  padding-bottom: var(--PSPDFKit-spacing-5);

  label {
    margin: 0;
  }

  @media (min-width: $stamp-mq) {
    margin-bottom: 0;
    padding-top: var(--PSPDFKit-spacing-6);
    padding-bottom: var(--PSPDFKit-spacing-6);
  }
}

@media (max-width: $stamp-mq) {
  .button {
    width: 100%;

    & ~ & {
      margin-top: var(--PSPDFKit-spacing-5);
    }
  }

  .group {
    width: 100%;
    margin-top: var(--PSPDFKit-spacing-5);

    @media (max-width: $screen-md) {
      margin-top: 0;
    }
  }
}

/* custom stamp preview */

.previewContainer {
  position: relative;
  overflow: hidden;
  border-radius: $borderRadius-5;
  border: 1px solid var(--PSPDFKit-StampViewComponent-previewContainer-border);
  background-color: var(--PSPDFKit-StampViewComponent-previewContainer-background);
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: var(--PSPDFKit-spacing-2) var(--PSPDFKit-spacing-5);
  margin-left: calc(-1 * var(--PSPDFKit-spacing-5));
  margin-right: calc(-1 * var(--PSPDFKit-spacing-5));

  @media (max-width: $screen-md) {
    margin-left: 0;
    margin-right: 0;
    padding: var(--PSPDFKit-spacing-5);
  }
}

.customStampPreviewContainer {
  background-color: transparent;
  text-align: center;
  color: transparent;
  text-transform: uppercase;

  canvas {
    max-width: 100%;
  }
}

.templateImageContainer {
  background-color: var(--PSPDFKit-StampViewComponent-templateImageContainer-background);
  border: 1px solid var(--PSPDFKit-StampViewComponent-templateImageContainer-border);
  border-radius: 2px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: percentage(math.div($item_margin_desktop * 2, $item_width_desktop));

  /*
   * FIXME (miguel):
   * !important is needed here because Layer.scss includes this rule, which
   * has too much specificity and a too broad scope:
   *
   * .withLayers > :last-child * {
   *   box-sizing: border-box;
   * }
   */
  box-sizing: content-box !important;

  @media (max-width: $screen-md) {
    padding: percentage(math.div($item_margin * 2, $item_width));
  }
}

.writeHere {
  position: absolute;
  top: 50%;
  margin-top: -0.5em;
  width: 100%;
  text-align: center;
  color: var(--PSPDFKit-StampViewComponent-writeHere-color);
  user-select: none;
}

.checkbox {
  display: flex;
  align-items: center;
  flex: 1;
  cursor: pointer;
}

.dateTimeLabels {
  display: flex;
  flex-direction: row;
  padding: var(--PSPDFKit-spacing-7) 0;
  justify-content: space-around;
}

/* stamp picker */

.stampPickerContainer {
  max-height: 50vh;
  overflow: auto;
  flex: 1 1 auto;
  -webkit-overflow-scrolling: touch;
  // Fix Chrome issue on overflow: auto containers where
  // the content is briefly painted out of its boundaries.
  transform: translateZ(0);

  @media (orientation: landscape) and (max-width: $screen-md) {
    max-height: 100%;
  }
}

.stampPickerList {
  width: 100%;
  padding: 0 percentage($item_margin_total_desktop * 0.01);
  margin-top: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  scroll-behavior: smooth;

  @media (max-width: $screen-md) {
    padding: 0 percentage($item_margin_total * 0.01);
  }
}

.stampPickerListItem {
  margin-top: percentage($item_margin_total_desktop * 2 * 0.01);
  list-style: none;
  width: percentage($item_width_desktop * 0.01);

  @media (max-width: $screen-md) {
    margin-top: percentage($item_margin_total * 2 * 0.01);
    width: percentage($item_width * 0.01);
  }
}

// In the stamp picker we render the available stamp types as clickable buttons

.selectStampButton {
  width: 100%;
  background-color: transparent;
  border: 1px solid transparent;
  align-items: stretch;
  padding: 0;
  display: flex;
  flex-direction: column;
  touch-action: auto;

  &:hover,
  &.active,
  &:focus {
    background-color: var(--PSPDFKit-StampViewComponent-selectStampButton-isHovered-background);
    color: var(--PSPDFKit-StampViewComponent-selectStampButton-isHovered-color);
    border-color: var(--PSPDFKit-StampViewComponent-selectStampButton-isHovered-border);

    /* stylelint-disable-next-line selector-max-specificity */
    .templateImageContainer {
      background-color: var(--PSPDFKit-StampViewComponent-templateImageContainer-background);
    }
  }
}

.title {
  color: var(--PSPDFKit-StampViewComponent-title-color);
  font-size: var(--PSPDFKit-fontSize-5);
  margin-bottom: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 100%;

  @media (max-width: $screen-sm) {
    padding-top: var(--PSPDFKit-spacing-6);
    padding-bottom: var(--PSPDFKit-spacing-6);
  }
}

.modalSubHeader {
  padding: var(--PSPDFKit-spacing-7) 0;
}

.annotationRect {
  position: absolute;
}

.modalDivider {
  margin: 0;
}

.spaceBetween {
  justify-content: space-between;
}
