.wrapper {
  padding: var(--PSPDFKit-spacing-7) var(--PSPDFKit-spacing-4);
}

.label {
  font-size: var(--PSPDFKit-fontSize-3);
  color: var(--PSPDFKit-FormDesignerTextInputComponent-label-color);
}

.input {
  padding: var(--PSPDFKit-spacing-5);
  background-color: var(--PSPDFKit-FormDesignerTextInputComponent-background-color);
  border-color: var(--PSPDFKit-FormDesignerTextInputComponent-border-color);
  font-size: var(--PSPDFKit-fontSize-3);
  font-weight: 500;
  color: var(--PSPDFKit-FormDesignerTextInputComponent-input-text-color);

  &:focus {
    background-color: var(--PSPDFKit-FormDesignerTextInputComponent-background-color);
  }

  &:active,
  &:hover,
  &:focus {
    border-color: var(--PSPDFKit-FormDesignerTextInputComponent-hover-border-color);
  }
}

.inputError {
  border-color: var(--PSPDFKit-FormDesignerTextInputComponent-error-color);

  &:focus {
    border-color: var(--PSPDFKit-FormDesignerTextInputComponent-error-color);
  }
}

.errorLabel {
  color: var(--PSPDFKit-FormDesignerTextInputComponent-error-color);
  font-size: var(--PSPDFKit-fontSize-2);
}
