@use '@/styles/config/variables' as *;

.btn {
  cursor: pointer;
  touch-action: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-radius: $border-radius-sm;
}

.btn:not([disabled]) {
  color: $white;
  background-color: desaturate(darken($widget-annotation-color, 25%), 15%);
  border-color: desaturate(darken($widget-annotation-color, 45%), 15%);

  /* stylelint-disable-next-line selector-max-specificity */
  &:hover {
    background-color: desaturate(darken($widget-annotation-color, 20%), 15%);
    outline: 0;
    color: $white;
  }

  /* stylelint-disable-next-line selector-max-specificity */
  &:active {
    background-color: desaturate(darken($widget-annotation-color, 30%), 15%);
  }
}

.btnFormDesigner {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
