@use '@/styles/config/variables' as *;

.root {
  display: block;
  user-select: none;
  color: $white;

  :global(.interactions-disabled) & {
    display: none;
  }

  div {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    flex: 1 0 auto;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
  }
}

.inner {
  display: block;
  overflow: hidden;
  border-radius: $border-radius-md;
}

.separator {
  background: darken($toolbar-color, 15%);
  height: 20px;
  margin: 0 6px;
  width: 1px;
}

.button {
  width: 38px !important;
  height: 34px !important;
  border-right: 1px solid darken($toolbar-color, 23%);

  &:last-child {
    border-right: 0;
  }

  i {
    width: 20px !important;
    height: 20px !important;
  }
}
