@use '../../mixins/input-type-range' as *;
@use '@/styles/config/variables' as *;

$thumb-color: var(--PSPDFKit-SliderComponent-thumb-color);
$thumb-hover-color: var(--PSPDFKit-SliderComponent-thumb-isHovered-color);
$thumb-border-width: 2px;
$thumb-border-color: var(--PSPDFKit-SliderComponent-thumb-border);
$thumb-height: 16px;
$thumb-width: 16px;
$track-color: var(--PSPDFKit-SliderComponent-track-color);
$track-progress-color: var(--PSPDFKit-SliderComponent-track-progress-color);
$track-border-width: 0;
$track-height: 8px;
$track-width: 100%;
$track-min-width: 70px;

.root {
  display: flex;
  align-items: center;
  font-size: inherit;
  color: inherit;
  margin: 5px;

  // prevent Safari clipping the thumb
  line-height: $thumb-height * 2;
}

.label {
  padding-right: 6px;
}

.input {
  position: relative;
  display: flex;
  align-items: center;
  width: 140px;

  /* stylelint-disable-next-line selector-max-specificity */
  input[type='range'] {
    @include input-type-range;
  }
}

.value {
  text-align: right;
  white-space: nowrap;
  margin-left: $track-height;
}
