.layer {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.highlight {
  mix-blend-mode: multiply;
  position: absolute;
  background-color: var(--PSPDFKit-SearchHighlight-background);

  // Edge and IE11 do not support mix blend mode, so we add opacity to the highlight
  :global(.PSPDFKit-browser-engine-edge) &,
  :global(.PSPDFKit-browser-engine-trident) & {
    opacity: 0.5;
  }

  &.focused {
    background-color: var(--PSPDFKit-SearchHighlight-isFocused-background);
  }
}
