@use '@/styles/config/variables' as *;

//== Slide Down

.slideDownEnter {
  transform: translate3d(0, -50%, 0);
  opacity: 0;
}

.slideDownEnterActive {
  transform: translate3d(0, 0, 0);
  transition: all 100ms ease-out;
  opacity: 1;
}

.slideDownExit {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.slideDownExitActive {
  transform: translate3d(0, -50%, 0);
  transition: all 120ms ease-in;
  opacity: 0;
}

//== Slide Up

.slideUpEnter {
  transform: translate3d(0, 50%, 0);
  opacity: 0;
}

.slideUpEnterActive {
  transform: translate3d(0, 0, 0);
  transition: all 100ms ease-out;
  opacity: 1;
}

.slideUpExit {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.slideUpExitActive {
  transform: translate3d(0, 50%, 0);
  transition: all 120ms ease-in;
  opacity: 0;
}

//== Move Down Signatures Validation Bar
// Ad-hoc classes for the signatures
// validation bar due to that we shouldn't
// use percentages values for translations
// since for that specific case for a smooth
// transition we need to specifically move it
// vertically by $toolbar-height pixels

.moveDownSignatureBarEnter {
  transform: translate3d(0, -$toolbar-height, 0);
}

.moveDownSignatureBarEnterActive {
  transform: translate3d(0, 0, 0);
  transition: all 100ms ease-out;
}

.moveDownSignatureBarExit {
  transform: translate3d(0, 0, 0);
}

.moveDownSignatureBarExitActive {
  transform: translate3d(0, -$toolbar-height, 0);
  transition: all 120ms ease-in;
}

//== Move Up Signatures Validation Bar

.moveUpSignatureBarEnter {
  transform: translate3d(0, $toolbar-height, 0);
}

.moveUpSignatureBarEnterActive {
  transform: translate3d(0, 0, 0);
  transition: all 100ms ease-out;
}

.moveUpSignatureBarExit {
  transform: translate3d(0, 0, 0);
}

.moveUpSignatureBarExitActive {
  transform: translate3d(0, $toolbar-height, 0);
  transition: all 120ms ease-in;
}

//== Fade

.fadeEnter {
  opacity: 0;
}

.fadeEnterActive {
  opacity: 1;
  transition: opacity 100ms ease-in;
}

.fadeExit {
  opacity: 1;
}

.fadeExitActive {
  opacity: 0;
  transition: opacity 150ms ease-out;
}

//== Scale In

.scaleInEnter {
  opacity: 0;
}

.scaleInEnterTop {
  transform: scale(0.7) translateY(-12px);
  transform-origin: top center;
}

.scaleInEnterBottom {
  transform: scale(0.7) translateY(12px);
  transform-origin: bottom center;
}

.scaleInEnterActive {
  transform: none;
  opacity: 1;

  transition-property: opacity, transform;
  transition-duration: 240ms;
  transition-timing-function: $ease-out-quart;
}

.scaleInExit {
  transform: none;
  opacity: 1;
}

.scaleInExitTop {
  transform-origin: top center;
}

.scaleInExitBottom {
  transform-origin: bottom center;
}

.scaleInExitActive {
  opacity: 0;
  transition-property: opacity, transform;
  transition-duration: 80ms;
  transition-timing-function: $ease-in-cubic;
}

.scaleInExitActiveTop {
  transform: scale(0.7) translateY(-12px);
}

.scaleInExitActiveBottom {
  transform: scale(0.7) translateY(12px);
}
