@use '@/styles/config/variables' as *;

.root {
  width: 90%;

  @media (min-width: 320px) {
    width: 300px;
  }

  > div,
  > button {
    margin: 0.5em 0;
  }
}

.progress {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  padding: 0.25em 0.5em;
  border-radius: $border-radius-sm;
  background-color: var(--PSPDFKit-PrintLoadingIndicator-progress-background);
  color: var(--PSPDFKit-PrintLoadingIndicator-progress-color);
  overflow: hidden;
  margin: 0.5em 0;
}

.progressBar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: var(--PSPDFKit-PrintLoadingIndicator-progressBar-background);
  z-index: -1;
}

.progressText {
  color: var(--PSPDFKit-PrintLoadingIndicator-progressText-color);
}
