@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

.wrapper {
  width: 100%;
  height: 100%;
  cursor: crosshair;
  position: relative;
}

.handles {
  height: 12px !important;
  width: 12px !important;
  background-color: #4636e3;
  border: 2px solid $color-white;
  border-radius: 50%;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.vertical {
  left: 50% !important;
  margin-left: -6px;
}

.handleTop {
  @extend .vertical;
  top: -6px !important;
}

.handleBottom {
  @extend .vertical;
  bottom: -6px !important;
}

.horizontal {
  top: 50% !important;
  margin-top: -6px;
}

.handleLeft {
  @extend .horizontal;
  left: -6px !important;
}

.handleRight {
  @extend .horizontal;
  right: -6px !important;
}

.handleTopLeft {
  left: -6px !important;
  top: -6px !important;
}

.handleTopRight {
  top: -6px !important;
  right: -6px !important;
}

.handleBottomLeft {
  left: -6px !important;
  bottom: -6px !important;
}

.handleBottomRight {
  bottom: -6px !important;
  right: -6px !important;
}

.cropBox {
  border: 1px solid $color-white;
  outline: 1px solid #717885;
  cursor: grab;
  user-select: none;

  &:active {
    user-select: none;
    cursor: grabbing;
  }
}
