@use '@/styles/config/variables' as *;

.root {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  line-height: 0;
  font-size: var(--PSPDFKit-fontSize-4);
}

.input {
  cursor: pointer;
  line-height: 0;
  display: flex;
  padding: 4px;
  border-radius: 100%;
  transition: all $transition-duration;
  touch-action: none;

  margin: 4px 3px;

  &:hover,
  &:active {
    background: var(--PSPDFKit-ColorPicker-isHovered-background);
  }
}

.focusRing {
  outline: 2px solid var(--PSPDFKit-ColorPicker-input-outline);
}

.swatch {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 100%;
  border: 2px solid var(--PSPDFKit-ColorPicker-swatch-border);
  position: relative;
  z-index: 2;
}

.inputIsActive {
  background: var(--PSPDFKit-ColorPicker-input-isActive-background) !important;
}

.swatchIsWhite,
.swatchIsTransparent {
  border: 2px solid var(--PSPDFKit-ColorPicker-swatch--transparent-border);
}

.swatchIsTransparent {
  background: var(--PSPDFKit-ColorPicker-swatch--transparent-background) !important;

  &::after {
    content: '';
    display: inline-block;
    width: 12px;
    border-bottom: 1px solid var(--PSPDFKit-ColorPicker-swatch--transparent-after-border);
    transform: rotate(-60deg) translate(-2px, 3px);
  }
}
