.container {
  height: 100%;
  width: 100%;
  isolation: isolate;
  user-select: none;

  .dragPreview {
    display: flex;
    pointer-events: none;
    position: absolute;
    overflow: hidden;

    left: 0;
    top: 0;
    right: 0;
    bottom: 0;

    z-index: 100;
  }

  /* stylelint-disable selector-max-specificity */
  .item {
    overflow: hidden;
    user-select: none;

    -webkit-touch-callout: none;

    .content {
      img,
      canvas {
        pointer-events: none;
      }
    }
  }
  /* stylelint-enable selector-max-specificity */
}
