@use '@pspdfkit/ui/theme' as *;

.container {
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: var(--PSPDFKit-spacing-6);
  box-sizing: border-box;
  background: var(--PSPDFKit-AnnotationsSidebar-container-background);
}

.empty {
  color: var(--PSPDFKit-AnnotationsSidebar--empty-color);
  padding: var(--PSPDFKit-spacing-2) var(--PSPDFKit-spacing-6);
  display: flex;
  justify-content: center;
  background-color: var(--PSPDFKit-AnnotationsSidebar--empty-background);
}

.annotationCounter {
  padding: var(--PSPDFKit-spacing-4);
  display: flex;
  background-color: var(--PSPDFKit-AnnotationsSidebar-annotationCounter-background);
  color: var(--PSPDFKit-AnnotationsSidebar-annotationCounter-color);
  font-weight: bold;
}

.pageNumber {
  font-size: var(--PSPDFKit-fontSize-3);
  font-weight: bold;
  padding: var(--PSPDFKit-spacing-5) var(--PSPDFKit-spacing-3);
  margin: 0;
  background: var(--PSPDFKit-AnnotationsSidebar-pageNumber-background);
  color: var(--PSPDFKit-AnnotationsSidebar-pageNumber-color);
  text-align: right;
}

.annotations {
  background-color: var(--PSPDFKit-AnnotationsSidebar-annotations-background);
  border-bottom: 1px solid var(--PSPDFKit-AnnotationsSidebar-annotations-border);
}

.icon {
  display: block;
  width: 24px;
  height: 24px;
  margin-right: var(--PSPDFKit-spacing-5);
  margin-left: var(--PSPDFKit-spacing-5);
  color: var(--PSPDFKit-AnnotationsSidebar-icon-color);
  fill: var(--PSPDFKit-AnnotationsSidebar-icon-color);
}

.layout {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  padding: var(--PSPDFKit-spacing-5) 0;
  margin-right: var(--PSPDFKit-spacing-4);
}

.layoutNavigationDisabled {
  cursor: default;
}

/* stylelint-disable selector-max-specificity */
.layout + .layout:before {
  content: '';
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  height: 1px;
  background-color: var(--PSPDFKit-AnnotationsSidebar-layout-background);
}
/* stylelint-enable selector-max-specificity */

.layoutContent {
  flex: 1;
  margin-right: var(--PSPDFKit-spacing-5);
  color: var(--PSPDFKit-AnnotationsSidebar-layoutContent-button-color);
  cursor: inherit;

  button {
    border: 0;
    padding: 0;
    background: transparent;
    cursor: inherit;
    color: var(--PSPDFKit-AnnotationsSidebar-layoutContent-button-color);
    font-style: inherit;
    font-variant: inherit;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    font-family: inherit;
    // We need to break the text to not overflow in IE/Edge.
    word-break: break-all;
    text-align: inherit;
  }
}

.footer {
  font-size: var(--PSPDFKit-fontSize-2);
  color: var(--PSPDFKit-AnnotationsSidebar-footer-color);

  p {
    padding: 0;
    margin: 0;
  }
}

.delete {
  opacity: 0;
  width: 24px;
  height: 24px;
  color: var(--PSPDFKit-AnnotationsSidebar-deleteIcon-color);
  border: 0;
  padding: 0;
  background: transparent;
  cursor: pointer;
  margin-right: var(--PSPDFKit-spacing-6);

  &:hover,
  &:focus,
  &:active,
  :hover > &,
  :focus > &,
  :active > & {
    opacity: 1;
  }
}
