.progress {
  animation: appear 100ms;
  flex-direction: column;
  display: flex;
  margin: 0 25px;
  max-width: 330px;
  width: 100%;
}

.progressBar {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  padding: 0.25em 0.5em;
  border-radius: var(--PSPDFKit-Progress-borderRadius);
  background-color: var(--PSPDFKit-Progress-progress-background);
  color: var(--PSPDFKit-Progress-progress-color);
  overflow: hidden;
  margin: 0.5em 0;
}

.progressFill {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: var(--PSPDFKit-Progress-progressBar-background);
  transition: width 500ms;
  z-index: -1;
}

.progressText {
  color: var(--PSPDFKit-Progress-progressText-color);
}

.loader {
  animation: appear 100ms;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 0 25px;
  max-width: 330px;
  width: 100%;
  transform: scale(2);
}

.loader .loaderDot {
  animation: loading 1.5s infinite ease-in-out;
  background-color: #a6adad;
  width: var(--PSPDFKit-Progress-dot-width);
  height: var(--PSPDFKit-Progress-dot-height);
  border-radius: 50%;
  transform: scale(0);
}

.loaderDot:not(:last-child) {
  margin-right: 4px;
}

/* stylelint-disable */
.loaderDot:nth-child(2) {
  animation-delay: 160ms;
}

.loaderDot:nth-child(3) {
  animation-delay: 320ms;
}
/* stylelint-enable */

@keyframes loading {
  // We need a small interval between each cycle, so we start late...
  20% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }

  // ... and finish early
  80% {
    transform: scale(0);
  }
}

@keyframes appear {
  // Wait before showing indicator so that we don't get a flash when loading finishes quickly
  0% {
    opacity: 0;
  }

  99% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
