@use '@/styles/config/variables' as *;

// Fix for an issue in Safari: This doesn't do any change to the existing design but without this,
// a rotated ListBox isn't visible unless we hover over it. This is a bug with Safari.
.listBoxRotated {
  outline: 1px solid transparent;
}

/* stylelint-disable-next-line selector-max-specificity */
.listBox:not([disabled]) option:checked {
  color: white;
  background: darken($widget-annotation-color, 10%)
    linear-gradient(
      0deg,
      darken($widget-annotation-color, 10%) 0%,
      darken($widget-annotation-color, 10%) 100%
    );
}
