.root {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  border: 0;
  pointer-events: none;
  z-index: -1;

  // It's possible to select the iFrame, which we don't want because it isn't pretty.
  // This makes the selection transparent.
  :global(.PSPDFKit-browser-engine-webkit) &,
  :global(.PSPDFKit-browser-engine-blink) & {
    /* stylelint-disable-next-line selector-max-specificity */
    &::selection {
      background: transparent;
    }
  }
}
