@use '@/styles/config/variables' as *;

.root {
  color: var(--PSPDFKit-Error-color);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box {
  max-width: 670px;
  display: flex;
  background: var(--PSPDFKit-Error-background);
  /* stylelint-disable */
  box-shadow: 0 3px 3px var(--PSPDFKit-shadow-color);
  /* stylelint-enable */
  border-radius: $border-radius-md;
  align-items: stretch;

  a {
    color: var(--PSPDFKit-Error-hyperlink-color);
    text-decoration: underline;
  }

  span {
    line-height: 30px;
  }
}

.error {
  background-color: var(--PSPDFKit-Error-error-background);
  padding: 15px 20px;
  border-top-left-radius: $border-radius-md;
  border-bottom-left-radius: $border-radius-md;
  display: flex;
  align-items: center;
}

.errorMessage {
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
}

.icon {
  width: 24px;
  height: 24px;
  fill: var(--PSPDFKit-Error-icon-color);
  border-radius: 12px;
  background-color: var(--PSPDFKit-Error-icon-background);
}
