@use '@/styles/config/variables' as *;

.wrapper {
  height: $es-wrapper-height;
  -webkit-overflow-scrolling: touch;

  @media (max-width: $screen-xs) {
    width: $es-wrapper-width-screen-xs;
    height: $es-wrapper-height-screen-xs;
    overflow: scroll;
  }
}

.typingUiContainer {
  height: $es-typingTab-typingUIContainer-height;
  background-color: var(--PSPDFKit-TypingTab-container-background-color);

  @media (max-width: $screen-xs) {
    height: $es-typingTab-typingUIContainer-height-screen-xs;
  }
}

.colorPickerContainer {
  // position the colorPicker aligned at the end of the signature area
  display: flex;
  justify-content: flex-end;
  width: $es-signature-area-width;
  margin-left: auto;
  margin-right: auto;
  padding-top: var(--PSPDFKit-spacing-3);

  @media (max-width: 660px) {
    width: $es-signature-area-width-screens-md;
  }

  @media (max-width: $screen-xs) {
    width: $es-wrapper-width-screen-xs;
    padding-top: 15px;
  }
}

.DropdownColorPicker {
  & span {
    fill: var(--PSPDFKit-TypingTab-Dropdown-ColorPicker-color);
  }

  &:focus,
  &:hover,
  &:active,
  &[data-focus-within] {
    background-color: var(--PSPDFKit-TypingTab-Dropdown-ColorPicker-background);
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background-color: var(--PSPDFKit-TypingTab-Dropdown-ColorPicker-background);
  }
}

.DropdownColorPicker div[role='button'] {
  background-color: var(--PSPDFKit-TypingTab-Dropdown-ColorPicker-background);
}

.colorSvg {
  width: 20px;
  height: 20px;
}

.inputContainer {
  display: block;
  width: $es-signature-area-width;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: 660px) {
    width: $es-signature-area-width-screens-md;
  }

  @media (max-width: $screen-xs) {
    width: $es-wrapper-width-screen-xs;
  }
}
.opaque {
  opacity: 0.5;
}

.input {
  background-color: inherit;
  margin-right: auto;
  margin-left: auto;
  // resetting border styles for component
  margin-bottom: 20px;
  border: none;
  border-bottom: 0.33px solid var(--PSPDFKit-TypingTab-input-border);
  font-size: 41px;
  letter-spacing: 0.02em;
  height: $es-typingTab-input-height;
  max-height: $es-typingTab-input-height;
  font-weight: bold;
  caret-color: rgba(0, 0, 0, 0.5);
  text-align: center;

  &:focus {
    background: var(--PSPDFKit-TypingTab-input-isFocused-background);
    border-bottom: 0.33px solid var(--PSPDFKit-TypingTab-input-border);
  }

  @media speech {
    &:focus-visible {
      outline-offset: initial;
      outline-style: auto;
      outline-color: initial;
    }
  }

  @media (max-width: $screen-xs) {
    width: $es-signature-area-width-screens-xs;
  }
}

.actionContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 0.5em;
  height: $es-actionContainer-height;

  @media (max-width: $screen-xs) {
    padding-bottom: 0.8em;
  }
}

.signHere {
  right: auto;
  left: 0;
  color: var(--PSPDFKit-DrawingTab-signHere-color);
  user-select: none;
}

.deleteSignature {
  color: var(--PSPDFKit-DrawingTab-deleteSignature-color) !important;
  border: none;
  padding: 0;
  width: auto;
}

.actionContainer > .deleteSignature {
  width: auto;
}

.signHere,
.deleteSignature {
  position: inherit;
  top: auto;

  width: 100%;
  text-align: center;
  transition: opacity 0.2s linear, visibility 0.2s;
}

.fontFamiliesPickerContainer {
  display: flex;
  flex-wrap: wrap;
  height: $es-typingTab-fontFamiliesPickerContainer-height;
  background-color: var(--PSPDFKit-TypingTab-fontFamilyPicker-background);
  border-top: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);

  @media (max-width: $screen-xs) {
    display: block;
    height: $es-typingTab-fontFamiliesPickerContainer-height-screen-xs;
    border: none;
    // Applied padding to push last element
    // above the native mobile keyboard
    padding-bottom: 80vh;
  }
}

.fontFamilyWrapperOnIE11 {
  border-right: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 3%;
  width: 50%;
  font-size: 30px;
  line-height: 20px;
  letter-spacing: 0.02em;
  background-color: var(--PSPDFKit-TypingTab-fontFamilyPicker-background);
  border-bottom: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);

  &:hover {
    background-color: var(--PSPDFKit-TypingTab-fontFamily-isHovered-background);
  }

  @media (max-width: $screen-xs) {
    height: 100px;

    & label {
      // disable multilines
      width: 88%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      height: 2em;
      line-height: 2em;
    }

    &:hover {
      background-color: var(--PSPDFKit-TypingTab-fontFamilyPicker-background);
    }
  }
}

.fontFamilyWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 3%;
  flex: 0 0 50%;
  font-size: 30px;
  line-height: 20px;
  letter-spacing: 0.02em;
  background-color: var(--PSPDFKit-TypingTab-fontFamilyPicker-background);
  border-bottom: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);

  &:hover {
    background-color: var(--PSPDFKit-TypingTab-fontFamily-isHovered-background);
  }

  @media (max-width: $screen-xs) {
    height: 100px;

    & label {
      // disable multilines
      width: 88%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      height: 2em;
      line-height: 2em;
    }

    &:hover {
      background-color: var(--PSPDFKit-TypingTab-fontFamilyPicker-background);
    }
  }
}

.fontFamilyWrapper:nth-child(odd) {
  border-right: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);

  &.fontFamilyWrapper:nth-last-child(2) {
    @media (min-width: $screen-sm) {
      border-bottom: none;
    }
  }

  @media (max-width: $screen-xs) {
    border-right: none;
  }
}

.fontFamilyWrapper:nth-child(3) {
  @media (max-width: $screen-xs) {
    border-bottom: 1px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);
  }
}

.fontFamilyWrapper:last-child {
  border-bottom: none;
}

.fontFamilyWrapper:last-child {
  @media (max-width: $screen-xs) {
    border-right: none;
  }
}

.fontFamilyRadio {
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 28px;
  height: 28px;
  border: 2px solid var(--PSPDFKit-TypingTab-fontFamilyPicker-border);
  border-radius: $es-input-border-radius;
  margin-right: 32px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  flex-shrink: 0;

  @media (max-width: 660px) {
    margin-right: 3%;
  }
}

.fontFamilyRadio:checked:before {
  background: var(--color-blue600);
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  margin: 4px auto;
  border-radius: $es-input-border-radius;
}

.fontLabel {
  display: flex;
  align-items: center;
  height: 100%;
}
