/** @PSPDFKit-define PasswordModal **/

@use '@pspdfkit/ui/src/theme/index.scss';

.container {
  max-width: 422px;
}

.icon {
  // Needs to be absolute so we can have the lock and unlock icon visible at the same time to make
  // a nice transition.
  position: absolute;
}

.icon > svg {
  width: 48px;
  height: 48px;
}

.icon > svg g {
  fill: currentColor;
  stroke: currentColor;
}

.icon-regular {
  color: var(--PSPDFKit-PasswordModal-icon--regular-color);
}

.icon-error {
  color: var(--PSPDFKit-PasswordModal-icon--error-color);
}

.icon-success {
  color: var(--PSPDFKit-PasswordModal-icon--success-color);
}

.iconBox {
  height: 48px;
  width: 48px;
  position: relative;
}

.alert {
  background: var(--PSPDFKit-PasswordModal-message--alert-background);
  color: var(--PSPDFKit-PasswordModal-message--alert-color);
  border-top: 2px solid var(--PSPDFKit-PasswordModal-message--alert-border);
  border-bottom: 2px solid var(--PSPDFKit-PasswordModal-message--alert-border);
}

.dialogButtons {
  display: flex;
  justify-content: flex-end;
}

//== Unlock Transition

.unlockEnter {
  transform: scale(0.5);
  opacity: 0;
}

.unlockEnterActive {
  transform: scale(1);
  transition: all 300ms cubic-bezier(0.895, 0.03, 0.685, 0.22);
  opacity: 1;
}

.unlockExit {
  transform: scale(1);
  opacity: 1;
}

.unlockExitActive {
  transform: scale(0.5);
  transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 0;
}
