.annotation {
  position: absolute;
  pointer-events: auto;
  border: 0;
  padding: 0;
  background: transparent;
  line-height: 1;

  &:hover,
  &:focus {
    cursor: pointer;
  }

  &[disabled] {
    cursor: auto;
  }
}

.icon svg {
  overflow: visible;
  height: 100%;
  width: 100%;
}

.iconActive :global(.PSPDFKit-Icon-CommentIndicator-Stroke-Shape) {
  stroke: var(--PSPDFKit-CommentMarkerAnnotation-accent-stroke);
  stroke-width: 5px;
}
