@use '@pspdfkit/ui/theme' as *;

.root {
  background: var(--PSPDFKit-DropdownMenu-background);
  margin: 0;
  list-style: none;
  border-radius: $borderRadius-4;
  padding: var(--PSPDFKit-spacing-6);
  color: var(--PSPDFKit-DropdownMenu-color);
  border: 1px solid var(--PSPDFKit-DropdownMenu-border);
  /* stylelint-disable */
  box-shadow: 0 3px 12px var(--PSPDFKit-shadow-color);
  /* stylelint-enable */
  user-select: none;

  &.hiddenMenu {
    padding: 0;
    border: 0;
    height: 0;
  }

  &.hiddenMenuEdge {
    visibility: hidden;
  }
}

.item {
  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-sm) &,
  :global(.PSPDFKit-width-md) & {
    display: none;
  }

  width: 100%;
  text-align: initial;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: var(--PSPDFKit-DropdownMenu-item-background);
  border-bottom: 1px solid var(--PSPDFKit-DropdownMenu-item-border);
  height: 34px;
  touch-action: pan-x;
  cursor: pointer;
  color: inherit;
  font: inherit;
  line-height: normal;
  padding: 0 11px;
}

.selectBox {
  display: flex;
  align-items: center;
  background: var(--PSPDFKit-DropdownMenu-button-background);
  border: 1px var(--PSPDFKit-DropdownMenu-button-border) solid;
  cursor: pointer;
  // border-radius: $border-radius-sm;
  color: var(--PSPDFKit-DropdownMenu-button-color);
  font-size: inherit;
  font-family: inherit;
  padding: var(--PSPDFKit-spacing-7);
  justify-content: space-between;
  border-radius: 3px;

  & .dropdownIcon {
    fill: var(--PSPDFKit-DropdownMenu-button-fill);
  }

  &:hover,
  &[data-focus-within] {
    background: var(--PSPDFKit-DropdownMenu-button-background);
    color: var(--PSPDFKit-DropdownMenu-button-color);
    /* stylelint-disable-next-line selector-max-specificity */
    .dropdownIcon {
      fill: var(--PSPDFKit-DropdownMenu-button-isHovered-color);
    }
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background: var(--PSPDFKit-DropdownMenu-button-background);
    color: var(--PSPDFKit-DropdownMenu-button-color);
    /* stylelint-disable-next-line selector-max-specificity */
    .dropdownIcon {
      fill: var(--PSPDFKit-DropdownMenu-button-isHovered-color);
    }
  }
}

.isSelected {
  background: var(--PSPDFKit-DropdownMenu-isSelected-background);
  color: var(--PSPDFKit-DropdownMenu-isSelected-color);
}
