@use '@/styles/config/variables' as *;

.root {
  width: 90%;

  @media (min-width: 320px) {
    width: 300px;
  }

  display: flex;
  align-items: center;
}

.message {
  padding-left: var(--PSPDFKit-spacing-3);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 48px;
}

.icon {
  width: 48px;
  height: 48px;
  display: block;
}
