.popover {
  & :global(.PSPDFKit-Popover-Arrow) {
    color: var(--PSPDFKit-ExpandingPopoverComponent-background-color);
    z-index: 1001;

    /* stylelint-disable-next-line */
    &:global(.PSPDFKit-Popover-Arrow-Right) {
      filter: drop-shadow(-1px 1px 1px rgba(43, 50, 59, 0.4));
    }

    /* stylelint-disable-next-line */
    &:global(.PSPDFKit-Popover-Arrow-Left) {
      filter: drop-shadow(1px 1px 1px rgba(43, 50, 59, 0.4));
    }

    /* stylelint-disable-next-line */
    &:global(.PSPDFKit-Popover-Arrow-Top) {
      filter: drop-shadow(0 3px 2px rgba(43, 50, 59, 0.4));
    }

    /* stylelint-disable-next-line */
    &:global(.PSPDFKit-Popover-Arrow-Bottom) {
      filter: drop-shadow(0 -1px 2px rgba(43, 50, 59, 0.1));
    }
  }

  /* stylelint-disable-next-line */
  & :global(.PSPDFKit-Popover) {
    background-color: var(--PSPDFKit-ExpandingPopoverComponent-background);
    box-shadow: 0 1px 2px rgba(43, 50, 59, 0.4), 0 3px 4px rgba(43, 50, 59, 0.1);
  }
}

.popoverHidden {
  display: none;
}

.container {
  width: 360px;
  text-align: left;
  border-radius: 6px;
  overflow: hidden;
}

.innerContainer {
  max-height: 400px;
  overflow-y: auto;
  overscroll-behavior: contain;
  display: flex;
  flex-direction: column;
}

.title {
  margin: 0;
  padding: var(--PSPDFKit-spacing-5) var(--PSPDFKit-spacing-4);
  font-weight: 600;
  font-size: var(--PSPDFKit-fontSize-3);
  color: var(--PSPDFKit-ExpandingPopoverComponent-title-color);
  border-bottom: 1px solid var(--PSPDFKit-ExpandingPopoverComponent-separator-border);
}

.content {
  background-color: var(--PSPDFKit-ExpandingPopoverComponent-content-background);
  color: var(--PSPDFKit-ExpandingPopoverComponent-content-color);
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.footer {
  border-top: 1px solid var(--PSPDFKit-ExpandingPopoverComponent-separator-border);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-md) &,
  :global(.PSPDFKit-width-sm) & {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: var(--PSPDFKit-spacing-5) var(--PSPDFKit-spacing-4);
  }
}

.footerButton {
  appearance: none;
  border: none;
  background: none;
  margin: 0;
  font-size: var(--PSPDFKit-fontSize-3);
  width: 100%;
  color: var(--PSPDFKit-ExpandingPopoverComponent-footerButton-color);
  padding: 1.125rem var(--PSPDFKit-spacing-4);

  &:hover {
    cursor: pointer;
    background-color: var(--PSPDFKit-ExpandingPopoverComponent-footerButton-hover-background);
  }
}

.shakeEffect {
  animation: shake 0.2s;
  animation-iteration-count: 1;
}

@keyframes shake {
  0% {
    transform: translate(2px, 0);
  }

  25% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(-2px, 0);
  }

  75% {
    transform: translate(0, 0);
  }

  80% {
    transform: translate(2px, 0);
  }

  100% {
    transform: translate(1px, 0);
  }
}
