@use '@/styles/config/variables' as *;

// stylelint-disable-next-line
@use './AnnotationToolbar';
@use './CropToolbarComponent';
@use '@pspdfkit/ui/theme' as *;
@use '@pspdfkit/ui/src/design-tokens/color';

/** @PSPDFKit-define FormDesignerToolbar **/

.formDesignerContainer {
  @extend .container;
  padding: 0;
  flex-direction: row;

  :global(.PSPDFKit-width-xs) &,
  :global(.PSPDFKit-width-md) &,
  :global(.PSPDFKit-width-sm) & {
    flex-direction: row-reverse;
  }
}

.formDesignerButton {
  @extend .button;
  margin: 0;
  display: inline-flex;
  padding: 10px;
  height: 100%;
  cursor: pointer;
}

.formDesignerButtonActive {
  color: white;
  background: #4636e3 !important;
}

.spacer {
  display: flex;
  flex: 1;
}
