@use '@/styles/config/variables' as *;

.responsiveGroup {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  display: flex;
  flex: 2;
  flex-direction: row;
  align-items: stretch;
  z-index: $zindex-responsive-group;

  &.primary {
    background-color: var(--PSPDFKit-ToolbarResponsiveGroup--primary-background);

    // TODO https://github.com/PSPDFKit/PSPDFKit-Web/pull/2213
    //
    // We make only the main (primary) toolbar scrollable since the annotations
    // toolbar (secondary) won't work in `position: fixed` because when fixed
    // the position is relative to the viewport and not the container and its
    // `top` position cannot be `0`. We should probably user the PlacerComponent.
    position: fixed;

    overflow-x: auto;

    // Smooth scrolling on webkit. FIXME (miguel): this is making the responsive
    // group buttons disappear once the stamps UI is shown. Temporarily
    // disabled. -webkit-overflow-scrolling: touch;

    // When there is overflow hides the scroll bar but keeps the container
    // scrollable.
    -ms-overflow-style: none;

    /* stylelint-disable selector-max-specificity */
    &.stickToBottom {
      // Since we use fixed positioning for the primary toolbar, we have to
      // manually change its position to the bottom in case a bottom toolbar
      // placement is configured.
      top: auto;
      bottom: 0;
    }
  }

  &.secondary {
    background: var(--PSPDFKit-ToolbarResponsiveGroup--secondary-background);

    /* stylelint-disable */
    box-shadow: -1px 2px 5px var(--PSPDFKit-shadow-color);
    /* stylelint-enable */
    border-bottom: 1px solid var(--PSPDFKit-ToolbarResponsiveGroup--secondary-border);
  }

  /* On Safari and IE11, on some cases when there are multiple elements on a
  secondary responsive group element, setting "right: 0" makes it overflow
  the bounds of the parent container even though the animation takes place
  using "translate3d" and thus the layout shouldn't be affected. Setting
  the property when the animation enters instead prevents this from
  happening. */
  :global(.PSPDFKit-browser-engine-webkit) &.secondary {
    right: unset;
  }

  :global(.PSPDFKit-browser-engine-trident) &.secondary {
    right: auto;
  }
}

.button {
  height: auto;

  .primary & {
    border-right: 1px solid var(--PSPDFKit-ToolbarResponsiveGroup-button--primary-border);

    /* stylelint-disable-next-line selector-max-specificity */
    .animatedArrow:before,
    .animatedArrow:after {
      border-color: var(--PSPDFKit-ToolbarResponsiveGroup-button--primary-arrow-border);
    }
  }

  .secondary & {
    border-right: 1px solid var(--PSPDFKit-ToolbarResponsiveGroup-button--secondary-border) !important;

    /* stylelint-disable-next-line selector-max-specificity */
    .animatedArrow:before,
    .animatedArrow:after {
      border-color: var(--PSPDFKit-ToolbarResponsiveGroup-button--secondary-arrow-border);
    }

    /* stylelint-disable-next-line selector-max-specificity */
    &:hover,
    &:focus,
    &:active,
    &.isActive {
      background: var(
        --PSPDFKit-ToolbarResponsiveGroup-button--secondary-arrow-isFocused-background
      ) !important;
    }

    /* stylelint-disable-next-line selector-max-specificity */
    svg {
      width: 24px;
      height: 24px;
      fill: var(--PSPDFKit-ToolbarResponsiveGroup-button-svg-fill);
    }
  }
}

.items {
  width: auto;
  height: auto;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.animatedArrow {
  background: transparent;
  border: 0;
  position: relative;
  width: 24px;
  height: 20px;

  // The arrow is not perfectly centered. The following `left` fixes that.
  left: 3px;

  &:before,
  &:after {
    content: '';
    position: absolute;
    width: calc(50% - 1px);
    left: 0;
    height: 50%;
    border-width: 2px;
  }

  // TOP
  &:before {
    top: 0;
    transform: rotate(-45deg);
    animation: arrow-left 250ms linear;
    transform-origin: 100% 100%;
    border-right-style: solid;
  }

  // BOTTOM
  &:after {
    top: 50%;
    transform: rotate(45deg);
    animation: arrow-bottom 250ms linear;
    transform-origin: 100% 0;
    border-right-style: solid;
  }
}

.slideRightEnter {
  transform: translate3d(50%, 0, 0);
  opacity: 0;
}

.slideRightEnterActive {
  transform: translate3d(0, 0, 0);
  transition: all 150ms ease-out;
  opacity: 1;
}

.slideRightExit {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.slideRightExitActive {
  transform: translate3d(50%, 0, 0);
  transition: all 120ms ease-in;
  opacity: 0;
}

/* As described earlier, we wait for the
animation classes to be set to make the
element grow. */
:global(.PSPDFKit-browser-engine-webkit),
:global(.PSPDFKit-browser-engine-trident) {
  .responsiveGroup.slideRightEnterActive,
  .responsiveGroup.slideRightEnterDone,
  .responsiveGroup.slideRightExit {
    right: 0;
  }
}

.responsiveGroup.slideRightEnterDone {
  position: absolute;
}

:global(.PSPDFKit-browser-engine-edge) {
  .slideRightEnter,
  .slideRightEnterActive,
  .slideRightExit,
  .slideRightExitActive {
    transform: none;
    transition: left 120ms ease-in;
  }

  .slideRightEnter {
    left: 100%;
    right: auto;
  }

  .slideRightEnterActive {
    left: 0;
    right: 0;
  }

  .slideRightExit {
    left: 0;
    right: 0;
  }

  .slideRightExitActive {
    left: 100%;
    right: auto;
  }
}

@keyframes arrow-left {
  0% {
    transform: rotate(0);
  }

  20% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(-45deg);
  }
}

@keyframes arrow-bottom {
  0% {
    transform: rotate(0);
  }

  20% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(45deg);
  }
}
