.root {
  display: flex;
  align-items: center;
}

.label {
  position: relative;
  display: flex;
  align-items: center;
  color: var(--PSPDFKit-CheckboxComponent-label-color);
  overflow: hidden;
  flex-wrap: nowrap;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.label::before {
  content: '';
  display: inline-block;
  height: 1rem;
  width: 1rem;
  border: 1px solid var(--PSPDFKit-CheckboxComponent-unchecked-border);
  border-radius: 3px;
  margin-right: 0.5rem;
}

/* stylelint-disable-next-line selector-max-specificity */
.label::after {
  position: absolute;
  display: inline-block;
  color: white;
  left: 0.1875rem;
  /* stylelint-disable-next-line */
  box-shadow: 0px 0.75px 1.5px rgba(0, 0, 0, 0.15);
}

/* stylelint-disable-next-line selector-max-specificity */
.input + .label::after {
  content: none;
}

/* stylelint-disable-next-line selector-max-specificity */
.input:checked + .label::after {
  content: '\2713';
}

/* stylelint-disable-next-line selector-max-specificity */
.input:checked + .label::before {
  border: 1px solid var(--PSPDFKit-CheckboxComponent-checked-border);
  /* stylelint-disable */
  box-shadow: inset 0px 0px 2px var(--PSPDFKit-shadow-color),
    inset 0px 0.75px 1.5px var(--PSPDFKit-shadow-color);
  /* stylelint-enable */
  background-color: var(--PSPDFKit-CheckboxComponent-checked-background);
}

/* stylelint-disable-next-line selector-max-specificity */
.input:focus + label::before {
  outline: rgb(59, 153, 252) auto 3px;
}

.input {
  opacity: 0;
  position: absolute;
  height: 1px;
  width: 1px;
  visibility: hidden;
}
