@use '@pspdfkit/ui/theme' as *;
@use '@/styles/config/variables' as *;

.commentThreads {
  position: absolute;
  font-size: var(--PSPDFKit-fontSize-3);
  top: 0;
  left: 0;
  transition: transform $ease-out-cubic 300ms;

  // Fallback value included for IE11, which doesn't support start/initial
  text-align: left;
  text-align: initial;

  -webkit-overflow-scrolling: touch;
}

.commentThreadsSmall {
  overflow-y: auto;
  background-color: var(--PSPDFKit-Viewport-background);
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  top: auto;
}

.commentThreadWrapper {
  position: absolute;

  transition: all 300ms $ease-out-cubic;
  transition-property: transform, box-shadow;

  box-shadow: 0px 0.75px 1.5px rgba(0, 0, 0, 0.15);
  border-radius: $borderRadius-3;
  cursor: pointer;
}

.commentThreadWrapper:hover {
  box-shadow: 0px 4px 6px rgba(43, 50, 59, 0.2), 0px 6px 16px rgba(43, 50, 59, 0.1);
}

.commentThreadWrapperExpanded,
.commentThreadWrapperExpanded:hover {
  z-index: 1;
  box-shadow: 0px 16px 32px rgba(43, 50, 59, 0.25), 0px 16px 32px rgba(43, 50, 59, 0.1);
}

.commentThread {
  width: 100%;
  height: 100%;
  border-radius: $borderRadius-3;
  background-color: var(--PSPDFKit-Comment-thread-backgroundColor);
  color: var(--PSPDFKit-Comment-thread-color);
}

.commentThreadsSmall .commentThread {
  border: 1px solid var(--PSPDFKit-Comment-separatorBold-color);
}

.comment {
  padding: var(--PSPDFKit-spacing-5);
}

.separatorSameAuthor {
  border-top: 1px solid var(--PSPDFKit-Comment-separator-color);
  margin-left: var(--PSPDFKit-spacing-5);
}

.separatorDifferentAuthor {
  border-top: 1px solid var(--PSPDFKit-Comment-separatorBold-color);
}

.header {
  display: flex;
  justify-content: space-between;
  line-height: 1;
}

.creatorName {
  font-weight: 600;
}

.commentDate {
  margin-top: var(--PSPDFKit-spacing-6);
  display: block;
  font-weight: 500;
  font-size: var(--PSPDFKit-fontSize-2);
  color: var(--PSPDFKit-Comment-accent-color);
}

.avatar {
  overflow: hidden;
  width: 32px;
  height: 32px;
  border-radius: 100%;
  border: 1px solid var(--PSPDFKit-Comment-accent-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar img {
  display: block;
  width: 32px;
  height: 32px;
  border-radius: 100%;
  background-color: var(--PSPDFKit-Comment-footer-color);
}

.commentText {
  margin-top: var(--PSPDFKit-spacing-6);
  margin-bottom: 0;
  word-break: break-word;
  white-space: pre-wrap;
}

.commentText p {
  margin: 0;
}

.commentActions {
  margin-top: var(--PSPDFKit-spacing-7);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.commentToggle {
  display: inline-block;
}

.commentFooter {
  display: flex;
  flex-direction: column;
  padding: var(--PSPDFKit-spacing-6) var(--PSPDFKit-spacing-5);
  background-color: var(--PSPDFKit-Comment-footer-backgroundColor);
  border-top: 1px solid var(--PSPDFKit-Comment-separatorBold-color);
  border-radius: 0px 0px 5px 5px;
  font-size: var(--PSPDFKit-fontSize-2);
  font-weight: 500;
}

.commentFooter:first-child {
  border-top: 0;
  border-radius: $borderRadius-3;
}

.moreComments {
  flex-direction: row;
  justify-content: space-between;

  display: flex;
  align-items: center;
}

.showMore {
  cursor: pointer;
  color: var(--PSPDFKit-Comment-accent-color);
}

.commentEditorInput {
  display: block;
  width: 100%;
  border: 1px solid var(--PSPDFKit-Comment-editor-borderColor);
  background-color: var(--PSPDFKit-Comment-editor-backgroundColor);
  border-radius: $borderRadius-5;
  height: 115px;
  outline-color: var(--PSPDFKit-Comment-accent-color);
  padding: var(--PSPDFKit-spacing-6);
  resize: none;
  font-size: inherit;
  color: inherit;
}

.commentFooterEditor {
  font-size: var(--PSPDFKit-fontSize-3);
  padding: var(--PSPDFKit-spacing-5);
}

.commentEditorActions {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-top: var(--PSPDFKit-spacing-6);
}

.commentDelete:focus,
.commentDelete:hover {
  color: var(--PSPDFKit-Comment-deleteIcon-color);
}
