@use '@pspdfkit/ui/src/design-tokens/color';

.wrapper {
  width: 100%;
  border-top: 1px solid var(--PSPDFKit-FormDesignerPopoverComponent-item-border-color);

  & > button {
    margin: 0;
    color: var(--PSPDFKit-FormDesignerExpandoComponent-button-text-color);
    background-color: var(--PSPDFKit-FormDesignerExpandoComponent-button-color);
    text-transform: uppercase;
    fill: var(--PSPDFKit-FormDesignerExpandoComponent-button-text-color);
    font-size: var(--PSPDFKit-fontSize-2);
    padding-left: 6px;
    letter-spacing: 0.03rem;

    &:hover {
      color: var(--PSPDFKit-FormDesignerExpandoComponent-button-text-hover-color);
      cursor: pointer;
      fill: var(--PSPDFKit-FormDesignerExpandoComponent-button-text-hover-color);
    }
  }
}

.controlContent {
  display: flex;
  padding: var(--PSPDFKit-spacing-7);
}

.icon {
  width: 12px;
  height: 22px;
  margin-right: var(--PSPDFKit-spacing-7);
}

.content {
  color: var(--PSPDFKit-FormDesignerExpandoComponent-content-color);
  fill: var(--PSPDFKit-FormDesignerExpandoComponent-content-color);
}
