.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.deadCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

.selectable {
  user-select: text;
}

.withLayers,
.withLayers > :first-child {
  height: 100%;
}

.withLayers > :last-child * {
  box-sizing: border-box;
}
