@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

.placeholder {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.stampAnnotation,
.widgetAnnotation {
  position: relative;
}

.loading,
.error {
  background-color: $gray-light;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.errorIcon svg {
  width: 24px;
  height: 24px;
  color: $error-red;
  border-radius: 12px;
  background-color: $white;
}

/**
 * AP streams and native appearance should not be simultaneously visible, but they do need to be rendered
 * so as to preserve interactivity.
 *
 * Here we hide the first child of the annotation container, and show the last one. The rule order below ensures
 * that a single child will be always visible.
 */
.APStreamParent {
  & > *:first-child {
    opacity: 0.0001 !important;
  }

  & > *:last-child {
    opacity: 1 !important;
  }
}
