@use '@/styles/config/variables' as *;

.layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: default;
}

.annotationRect {
  position: absolute;
  border: 0;
  padding: 0;
  line-height: 0;
  /* stylelint-disable-next-line declaration-block-no-shorthand-property-overrides */
  font: inherit;
}
