@import './index';

.wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
}

.pickerWidget {
  align-items: center;
  background-image: url('data:image/svg+xml;base64,');
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  color: white;
  cursor: pointer;
  height: 100%;
  justify-content: center;
  position: absolute;
  overflow: hidden;
  top: 0;
  z-index: 9999;

  // Make sure you update these values in tandem with left and width of
  // the picker
  //
  right: -16px;
  width: 16px;
}

.pickerWidget:focus-within {
  background-image: url('data:image/svg+xml;base64,');
}

.picker {
  appearance: none;
  position: absolute;
  top: 0;
  height: 100%;
  opacity: 0;

  // Make sure you update these values in tandem with right and width of
  // the picker widget
  //
  // We need to make sure this field is wider and offset to the left so
  // the non-clickable areas of the field in Chrome cannot be clicked.
  //
  left: -40px;
  width: 80px;

  &::-webkit-calendar-picker-indicator {
    background: transparent;
    bottom: 0;
    color: transparent;
    height: auto;
    position: absolute;
    left: 0;
    right: 0;
    width: auto;
    top: 0;
  }
}
