$dot-size: 6px;

.loader {
  display: flex;
}

.loaderDot {
  background-color: #a6adad;
  width: $dot-size;
  height: $dot-size;
  border-radius: 50%;
  transform: scale(0);
}

.loaderDot:not(:last-child) {
  margin-right: 4px;
}

.loaderShown .loaderDot {
  animation: loading 1.5s infinite ease-in-out;
}

/* stylelint-disable */
.loaderDot:nth-child(2) {
  animation-delay: 160ms;
}

.loaderDot:nth-child(3) {
  animation-delay: 320ms;
}
/* stylelint-enable */

@keyframes loading {
  // We need a small interval between each cycle, so we start late...
  20% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }

  // ... and finish early
  80% {
    transform: scale(0);
  }
}
