@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

.root,
.dropdown,
.withDropdown {
  height: $toolbar-height;
  margin: 0;
  border: 0;
  background-color: transparent;

  // Explicitly transitioning background-color here, used by `&:hover`.
  // Transitioning `all` lead to flickering in Edge.
  // https://github.com/PSPDFKit/PSPDFKit-Web/issues/522
  transition: background-color $transition-duration, color $transition-duration;

  .icon {
    width: 24px;
    height: 24px;
    fill: currentColor;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }

  &:hover,
  &:focus,
  &[data-focus-within],
  &:active {
    background-color: var(--PSPDFKit-ToolbarButton-dropdown-isFocused-background);
    color: var(--PSPDFKit-ToolbarButton-dropdown-isFocused-color);
  }

  // Keep this separate otherwise the styles above won't work
  // when :focus-within is not supported.
  &:focus-within {
    background-color: var(--PSPDFKit-ToolbarButton-dropdown-isFocused-background);
    color: var(--PSPDFKit-ToolbarButton-dropdown-isFocused-color);
  }
}

.root,
.dropdown {
  min-width: $toolbar-height;
  padding: 0 11px;
}

.wrapper {
  display: flex;
}

.dropdown {
  padding: 0 5px;
  color: inherit;
}

.discreteDropdown {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: $toolbar-height;
  cursor: pointer;
}

.dropdownIcon {
  opacity: 0.8;
}

:hover,
:focus,
:active {
  & > .dropdownIcon {
    opacity: 1;
  }
}

.withDropdown {
  padding: 0;
  border: 0;

  .root {
    padding-right: 0;
  }

  .root,
  .dropdown {
    transition: none;
  }
}

.withDropdownIcon .root {
  padding-left: 0;
}

.withDropdownIdle {
  .root,
  .dropdown {
    background: transparent;
  }
}

.withLabel {
  margin-right: 11px;
}

.isDisabled,
.root:disabled {
  opacity: var(--PSPDFKit-ToolbarButton-isDisabled-opacity);

  /* stylelint-disable-next-line selector-max-specificity */
  &:hover,
  &:focus,
  &:active {
    opacity: 0.5;
  }

  /* stylelint-disable-next-line selector-max-specificity */
  i {
    fill: var(--PSPDFKit-ToolbarButton-isDisabled-child-fill);
  }
}

.isActive {
  &,
  &:hover,
  &:focus,
  &:active {
    background: var(--PSPDFKit-ToolbarButton-isActive-background) !important;
    color: var(--PSPDFKit-ToolbarButton-isActive-color) !important;
  }
}
