/* @PSPDFKit-define ThumbnailsSidebar */
@use '@/styles/config/variables' as *;

.row {
  display: flex;
  // IE11 fallback
  justify-content: space-around;
  justify-content: space-evenly;
  background: var(--PSPDFKit-ThumbnailsSidebar-gridView-row-background);
  position: relative;
}

// Edge accepts the space-evenly rule but doesn't implement it...
:global(.PSPDFKit-browser-engine-edge) .row {
  justify-content: space-around !important;
}

.column {
  text-align: center;
  border-radius: 10px;
}

.columnPreview {
  display: flex;
  align-items: center;
  justify-content: center;
}

.moveCursor {
  position: relative;
  width: 3px;
  height: calc(100% - 8px);
  background: #5e5ceb;
  pointer-events: none;
  left: 50%;

  &Disabled {
    background: var(--PSPDFKit-ThumbnailsSidebar-gridView-moveCursorDisabled-background);
    opacity: 0.1;
  }

  &Preview {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);

    &End {
      transform: translateX(calc(-100% - 48px)) translateY(-50%);
    }
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 3px;
    background: var(--PSPDFKit-ThumbnailsSidebar-gridView-moveCursorDisabled-background);
    left: 50%;
    transform: translateX(-50%);

    @media (min-width: $screen-sm) {
      width: 24px;
    }
  }

  /* stylelint-disable selector-max-specificity */
  &:not(&Disabled) {
    &::before,
    &::after {
      background: #5e5ceb;
    }
  }
  /* stylelint-enable selector-max-specificity */

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }
}

.thumbnail {
  background-color: var(--PSPDFKit-ThumbnailsSidebar-gridView-thumbnail-background);
  box-shadow: 0 1.5px 2px var(--PSPDFKit-ThumbnailsSidebar-gridView-thumbnail-boxShadow),
    0 3px 6px var(--PSPDFKit-ThumbnailsSidebar-gridView-thumbnail-boxShadow);
  overflow: hidden;
  transition: opacity 160ms;
}

.thumbnail:not(.thumbnailClickDisabled) {
  cursor: pointer;
}

.thumbnailSelected {
  box-shadow: 0 0 0 4px var(--PSPDFKit-ThumbnailsSidebar-gridView-thumbnail-isSelected-boxShadow);
  border-radius: 1px;
}

.thumbnailBeingDragged {
  opacity: 0.3;
}

.label {
  display: inline-block;
  text-align: center;
  width: auto;
  min-width: 48px;
  background-color: var(--PSPDFKit-ThumbnailsSidebar-gridView-label-background);
  color: var(--PSPDFKit-ThumbnailsSidebar-gridView-label-color);
  text-shadow: 0 1.5px 3px rgb(0 0 0 / 0.3);
  border-radius: 3px;
  padding: 0 4px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.labelSelected {
  background-color: var(--PSPDFKit-ThumbnailsSidebar-gridView-label-isSelected-background);
  color: var(--PSPDFKit-ThumbnailsSidebar-gridView-label-isSelected-color);
}

.engine-blink {
  img,
  canvas {
    // This is a workaround for https://github.com/PSPDFKit/PSPDFKit/issues/26952
    // Chrome does not like that we add fetched image as a child later on, when the image
    // is descendant of a transformed element, and refreshes the
    // DOM with the fetched image only after UI interaction (scroll, hover etc.) unless
    // we enable 3d acceleration dirrectly on the image.
    // https://blog.postman.com/ui-repaint-issue-on-chrome/
    transform: translate3d(0, 0, 0);
  }
}
