.root {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  opacity: 0.97;
  padding: var(--PSPDFKit-spacing-4) var(--PSPDFKit-spacing-7);
}

.signatureInfoContainer {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.signatureValidationStatusIconContainer {
  margin-right: var(--PSPDFKit-spacing-5);
}

.signatureValidationStatusIcon {
  width: 24px;
  height: 24px;
  display: block;
}

.signatureValidationCloseButton {
  display: flex;
  justify-content: center;
  background-color: transparent;
  border: none;
  padding: 0 0 0 var(--PSPDFKit-spacing-5);
}

// Prevent default button hover background
.signatureValidationCloseButton:hover {
  background-color: transparent;
}

.signatureValidationCloseIcon {
  flex-basis: 24px;
  width: 24px;
  height: 24px;
}

.status-valid {
  background-color: var(--PSPDFKit-SignaturesValidationStatusBar-Valid-backgroundColor);
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Valid-color);
}

.status-valid-icon {
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Icon-Valid-color);
}

.status-warning {
  background-color: var(--PSPDFKit-SignaturesValidationStatusBar-Warning-backgroundColor);
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Warning-color);
}

.status-warning-icon {
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Icon-Warning-color);
}

.status-error {
  background-color: var(--PSPDFKit-SignaturesValidationStatusBar-Error-backgroundColor);
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Error-color);
}

.status-error-icon {
  color: var(--PSPDFKit-SignaturesValidationStatusBar-Icon-Error-color);
}
