@use '@/styles/config/variables' as *;

.layer {
  // This gives us highlights that blend correctly (keeping the black text black)
  // http://caniuse.com/#feat=css-mixblendmode
  mix-blend-mode: multiply;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  user-select: text;

  // Required for hit testing component since it sets the cursor in a parent node
  cursor: inherit;

  :global(.PSPDFKit-browser-engine-webkit) & {
    // Makes a page-div selection transparent
    /* stylelint-disable-next-line selector-max-specificity */
    &::selection {
      background: transparent;
    }
  }

  :global(.PSPDFKit-browser-system-android) &,
  :global(.PSPDFKit-browser-engine-gecko) &,
  :global(.PSPDFKit-browser-system-windows) & {
    opacity: 0.2;
  }

  :global(.PSPDFKit-browser-engine-gecko) & {
    /* stylelint-disable-next-line selector-max-specificity */
    .overlay {
      width: 100%;
      left: 0;
      bottom: 0;
      right: 0;
      position: absolute;
      top: 100%;
      z-index: -1;
      cursor: default;
      user-select: none;
    }
  }
}

.linePadding {
  display: block;
  position: absolute;

  // Extend the padding to the end of the viewport.
  width: 100vw;
  height: 100vh;

  // Use default cursor when hovering the padding.
  cursor: inherit;

  // Move the padding to background to allow selecting text even if lines are not ordered from top-left.
  z-index: $zindex-background;

  // Disabling text selection for the padding prevents wonky selection behavior on mobile Chrome.
  user-select: none;

  // Setting background color of the padding selection to transparent fixes selection highlight in mobile Safari.
  /* stylelint-disable-next-line selector-max-specificity */
  ::selection {
    background-color: transparent !important;
  }
}

.line {
  display: block;
  position: absolute;
  text-align: left;
  white-space: pre;
  margin: 0;
  color: transparent;
  transform-origin: 0 0 0;
  // Force an arbitrary font size, the scaleX/Y transforms make it irrelevant
  line-height: 1;
  font-family: Arial, sans-serif;
  font-weight: normal;
  // See .enableTextSelection
  user-select: none;
  // The `!important` here is needed because TextMeasurement will otherwise overwrite it
  font-size: 16px !important;

  :global(.PSPDFKit-browser-engine-gecko) & {
    /* stylelint-disable-next-line selector-max-specificity */
    ::selection {
      background-color: $custom-text-selection-color;
    }
  }

  /* stylelint-disable-next-line selector-max-specificity */
  :global(.PSPDFKit-browser-engine-blink.PSPDFKit-browser-system-windows) &,
  /* stylelint-disable-next-line selector-max-specificity */
  :global(.PSPDFKit-browser-engine-blink.PSPDFKit-browser-system-android) & {
    /* stylelint-disable-next-line selector-max-specificity */
    span::selection {
      /* Since Chrome 83 there's a regression that's making transparent text visible during
      a selection. Setting the selection pseudo-element fixes it. However, we need to
      customize the background color in this case. Since each platform use a distinctive
      color, we can delegate the decision to the engine by using the "highlight" value.
      This value was deprecated in CSS3 but still works on Chrome.

      Once this bug has been fixed on Mac, the workaround is only needed on Windows and Android
      https://bugs.chromium.org/p/chromium/issues/detail?id=1083232 is resolved. */
      background-color: highlight;
      color: transparent;
    }
  }

  :global(.PSPDFKit-browser-system-android) &,
  :global(.PSPDFKit-browser-system-windows) & {
    /* stylelint-disable-next-line selector-max-specificity */
    span::selection {
      background-color: $custom-text-selection-color;
      color: $custom-text-selection-color;
    }
  }

  :global(.interactions-disabled) & {
    user-select: none;
  }

  /**
   * Sometimes, the height of text lines is very narrow and so there is a gap between individual
   * lines. This makes text selection vary since the gap causes a fall-through to the root element
   * which will cause every element to be selected.
   *
   * To fix this, we're making the selectable area a bit larger (like setting a line-height: 1.5em).
   * We also don't center the text line within the extended area. This is necessary because in the
   * line below will overlap and thus it might prevent text selection in the previous line. Since we
   * assume that the next text line also appears as a next sibling in the DOM, we can also assume
   * that the next text line will overlap and thus the area remains clickable. This is why most ouf
   * the exetension is drawn underneath the textline.
   *
   * | Textline A  |                             Looking from this direction will hide the extension
   * | (extension) | Textline B  |           <-- of textline A behind textline B so both remain
   *               | (extension) |               selectable as they are expected to.
   *
   * This is not necessary on gecko since we apply the overlay trick there.
   * On Chrome since version 83, this is preventing lines selection to work properly.
   */
  &:before {
    width: 100%;
    height: 150%;
    position: absolute;
    top: -10%;
    content: '';

    /* stylelint-disable-next-line selector-max-specificity */
    :global(.PSPDFKit-browser-engine-gecko) &,
    :global(.PSPDFKit-browser-engine-blink) & {
      display: none;
    }
  }
}

/* Since Chrome 83, the workaround for the gap between individual lines
with the :before pseudo-element stopped working for Chrome. Instead,
placing an element after the span with the content seems to do the trick.
Since this element is going to be place at the end of the content span,
we need to set its height as 50% of the parent (which in practice will
be as height as the content span) to achieve the desired line-height: 1.5
effect. */
.chromeSelectionWorkaround {
  position: absolute;
  width: 100%;
  height: 50%;
}

.lineTextCursor {
  /**
   * Since the HitTestingComponent works by setting a parent to the text layer which occasionally
   * defines a `cursor: pointer`, we can not always force a vertical cursor here (since that would
   * overwrite the parent's `pointer`). To solve this, we added a global CSS class
   * `.PSPDFKit-CursorPointer` which will force text lines not to specify a custom cursor and thus
   * the `pointer` can be inherited from the `HitTestingComponent`.
   */
  cursor: text;

  &.vertical {
    cursor: vertical-text;
  }

  :global(.PSPDFKit-CursorPointer) & {
    cursor: inherit !important;
  }
}

.enableTextSelection {
  user-select: text;
}

.debug {
  outline: 1px solid red;
  color: red;
}

// This class name is only a placeholder to prevent the text lines having an "undefined"
// class instead. This class name is used by `NormalizeSelection`. Removing it can break
// the inline markup toolbar.
.selectableText {
  display: block;
}
