@use '@/styles/config/variables' as *;
@use '@pspdfkit/ui/theme' as *;

.imageAnnotation,
.stampAnnotation {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  img,
  canvas {
    left: 0;
    top: 0;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
  }
}

.imageAnnotation-loading,
.imageAnnotation-error {
  background-color: var(--PSPDFKit-ImagePreview-imageAnnotation--error-background);
  position: relative;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.errorIcon svg {
  width: 24px;
  height: 24px;
  color: var(--PSPDFKit-ImagePreview-imageAnnotation--error-icon-color);
  border-radius: 12px;
  background-color: var(--PSPDFKit-ImagePreview-imageAnnotation--error-icon-background);
}

.notContainedImg img,
.notContainedImg canvas {
  width: auto;
  height: auto;
}

/* stylelint-disable selector-max-specificity */
.containedImg.wideImg img,
.containedImg.wideImg canvas {
  width: 100%;
  height: auto;
}

.containedImg.highImg img,
.containedImg.highImg canvas {
  width: auto;
  height: 100%;
}

/* stylelint-enable selector-max-specificity */
.placeHolder {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
